import request from "@/utils/request";

//创建订单
export function createOrder(data){
    return request({
        url: "/order/create",
        method: "put",
        data: data
    })
}

//获得订单列表
export function getOrderList(params){
    return request({
        url: "/order/list",
        method: "get",
        params: params

    })
}

//支付订单

export function payOrder(data){
    return request({
        url: '/order/pay',
        method: "post",
        data:data
    })
}

//取消订单
export function cancelOrder(data){
    return request({
        url: "/order/cancel",
        method: "post",
        data: data
    })
}