<template>
  <div class="simple-payment-container">
    <div class="payment-content">
      <!-- 顶部标题 -->
      <div class="payment-header">
        <h2>订单支付</h2>
        <p>请完成支付以确认订单</p>
      </div>

      <!-- 订单信息卡片 -->
      <el-card class="order-info-card">
        <div class="order-info">
          <div class="order-no">
            <el-icon><Document /></el-icon>
            订单号：{{ orderInfo.orderNo }}
          </div>
          <div class="order-amount">
            <span class="amount-label">应付金额：</span>
            <span class="amount-value">¥{{ orderInfo.totalAmount }}</span>
          </div>
        </div>
      </el-card>

      <!-- 支付方式选择 -->
      <el-card class="payment-method-card">
        <h3>选择支付方式</h3>
        <div class="payment-options">
          <div
            class="payment-option"
            :class="{ active: selectedPayment === 1 }"
            @click="selectedPayment = 1"
          >
            <img src="@/assets/images/wechat-pay.png" alt="微信支付" />
            <span>微信支付</span>
          </div>
          <div
            class="payment-option"
            :class="{ active: selectedPayment === 2 }"
            @click="selectedPayment = 2"
          >
            <img src="@/assets/images/alipay.png" alt="支付宝" />
            <span>支付宝</span>
          </div>
        </div>
      </el-card>

      <!-- 密码输入区域 -->
      <el-card class="password-card">
        <h3>支付密码</h3>
        <div class="password-input-section">
          <div class="password-input-container">
            <!-- ✅ 改为普通密码输入框 -->
            <el-input
              v-model="password"
              type="password"
              placeholder="请输入6~20位支付密码"
              minlength="6"
              maxlength="20"
              show-password
              class="password-input"
              :disabled="paying"
              @keyup.enter="confirmPayment"
            />
          </div>
          <p class="password-tip">请输入6~20位数字或字母支付密码</p>
        </div>
      </el-card>

      <!-- 操作按钮 -->
      <div class="payment-actions">
        <el-button 
          @click="goBack" 
          class="back-btn"
          :disabled="paying"
        >
          返回订单
        </el-button>
        <el-button 
          type="primary" 
          @click="confirmPayment" 
          class="pay-btn"
          :loading="paying"
          :disabled="!canPay"
        >
          {{ paying ? '支付中...' : `确认支付 ¥${orderInfo.totalAmount}` }}
        </el-button>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, computed } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { ElMessage } from 'element-plus'
import { Document } from '@element-plus/icons-vue'
import { payOrder as apiPayOrder } from '@/api/order'

const route = useRoute()
const router = useRouter()

// 订单信息
const orderInfo = ref({
  id: '',
  orderNo: '',
  totalAmount: '0.00'
})

// 支付方式
const selectedPayment = ref(1)

// 密码相关
const password = ref('')
const paying = ref(false)

// ✅ 支付按钮是否可用（长度要求6~20）
const canPay = computed(() => {
  return orderInfo.value.id && password.value.length >= 6 && password.value.length <= 20
})

// 加载订单数据
const loadOrderData = async () => {
  const { id, orderNo, amount } = route.query

  if (!id && !orderNo) {
    ElMessage.error('订单信息不存在')
    router.back()
    return
  }

  orderInfo.value = {
    id: id || '',
    orderNo: orderNo || `ORDER${Date.now()}`,
    totalAmount: amount || '0.00',
  }
   console.log("订单信息：",orderInfo.value)
}

// ✅ 点击确认支付时才发请求
const confirmPayment = async () => {
  if (!orderInfo.value.id) {
    ElMessage.error('订单信息异常，无法支付')
    return
  }

  if (password.value.length < 6 || password.value.length > 20) {
    ElMessage.error('请输入6~20位支付密码')
    return
  }

  paying.value = true
  try {
    const paymentData = {
      id: orderInfo.value.id,
      payMethod: selectedPayment.value,
      totalAmount: orderInfo.value.totalAmount,
      password: password.value
    }

    console.log('发送支付请求:', paymentData)
    const res = await apiPayOrder(paymentData)
    
    if (res.code === 200) {
      ElMessage.success('支付成功！')
      setTimeout(() => {
        router.push('/order')
      }, 1500)
    } else {
      ElMessage.error(res.message || '支付失败，请检查密码是否正确')
      password.value = ''
    }
  } catch (error) {
    console.error('支付请求失败:', error)
    ElMessage.error('支付失败，请重试')
    password.value = ''
  } finally {
    paying.value = false
  }
}

const goBack = () => router.back()

onMounted(() => loadOrderData())
</script>

<style scoped>
.simple-payment-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #fdf6e3 0%, #faf4eb 100%);
  padding: 40px 20px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.payment-content {
  width: 100%;
  max-width: 500px;
}

.payment-header {
  text-align: center;
  margin-bottom: 30px;
}

.payment-header h2 {
  margin: 0;
  color: #b38b2d;
  font-size: 28px;
  font-weight: 600;
  background: linear-gradient(135deg, #d6bb70, #b38b2d);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.payment-header p {
  margin: 8px 0 0;
  color: #8a7c5a;
  font-size: 14px;
}

.order-info-card,
.payment-method-card,
.password-card {
  margin-bottom: 20px;
  border-radius: 12px;
  border: 1px solid #e8dfc8;
  background: rgba(255, 253, 247, 0.9);
}

:deep(.el-card__body) {
  padding: 20px;
}

.order-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.order-no {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #5d5340;
  font-weight: 500;
}

.order-amount {
  text-align: right;
}

.amount-label {
  color: #8a7c5a;
  font-size: 14px;
}

.amount-value {
  color: #d6bb70;
  font-weight: bold;
  font-size: 24px;
}

.payment-method-card h3,
.password-card h3 {
  margin: 0 0 16px 0;
  color: #b38b2d;
  font-size: 16px;
  font-weight: 600;
}

.payment-options {
  display: flex;
  gap: 12px;
}

.payment-option {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 16px;
  border: 2px solid #f0e9d7;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  background: white;
}
.payment-option img {
  width: 28px;
  height: 28px;
  object-fit: contain;
  margin-right: 4px;
}


.payment-option:hover {
  border-color: #d6bb70;
}

.payment-option.active {
  border-color: #d6bb70;
  background: rgba(214, 187, 112, 0.05);
  box-shadow: 0 2px 8px rgba(214, 187, 112, 0.2);
}

.password-input-section {
  text-align: center;
}

.password-input-container {
  max-width: 300px;
  margin: 0 auto 12px;
}

:deep(.password-input .el-input__inner) {
  height: 50px;
  font-size: 18px;
  text-align: center;
  border: 2px solid #d6bb70;
  border-radius: 8px;
}

.password-tip {
  color: #8a7c5a;
  font-size: 14px;
  margin: 0;
}

.payment-actions {
  display: flex;
  gap: 12px;
  margin-top: 30px;
}

.back-btn,
.pay-btn {
  flex: 1;
  height: 48px;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 500;
}

.pay-btn {
  background: linear-gradient(135deg, #d6bb70, #c4a85f);
  border: none;
  color: white;
}

.pay-btn:hover:not(:disabled) {
  background: linear-gradient(135deg, #c4a85f, #b38b2d);
  box-shadow: 0 4px 12px rgba(214, 187, 112, 0.3);
}

.pay-btn:disabled {
  background: #cccccc;
  cursor: not-allowed;
}

@media (max-width: 768px) {
  .payment-actions {
    flex-direction: column;
  }
}
</style>
