<template>
  <el-card
    class="cake-card"
    :body-style="{ padding: '0px' }"
  >
    <!-- 图片展示区域 -->
    <div class="image-container">
      <img
        :src="getImageUrl(cake.image)"
        class="cake-image"
        @error="handleImageError"
        alt="蛋糕图片"
      >
      <div v-if="cake.tags?.length" class="tags-container">
        <el-tag
          v-for="tag in cake.tags"
          :key="tag"
          size="small"
          class="tag-item"
        >
          {{ tag }}
        </el-tag>
      </div>
    </div>

    <!-- 商品信息区域 -->
    <div class="cake-info">
      <!-- 名称区域 -->
      <div class="name-section">
        <h3 class="cake-name">{{ cake.name }}</h3>
        <div class="action-buttons">
          <slot name="actions"></slot>
        </div>
      </div>
      
      <!-- 价格区域 -->
      <div class="price-section">
        <div class="price-main">
          <span class="current-price">¥{{ minPrice.toFixed(2) }}</span>
          <span class="from-text">起</span>
        </div>
        <span class="weight">{{ specsCount }}种规格</span>
      </div>

      <el-button
        type="primary"
        class="add-cart-btn"
        @click.stop="showSpecDialog = true"
      >
        <i class="el-icon-shopping-cart-1"></i>
        加入购物车
      </el-button>
    </div>

    <!-- 规格选择对话框 -->
    <el-dialog
      v-model="showSpecDialog"
      :title="`选择${cake.name}规格`"
      width="500px"
      append-to-body
      class="golden-dialog"
    >
      <div class="spec-dialog-content">
        <div class="spec-list">
          <div
            v-for="spec in cake.specs"
            :key="spec.id"
            class="spec-item"
            :class="{ active: selectedSpecId === spec.id }"
            @click="selectSpec(spec)"
          >
            <div class="spec-info">
              <span class="spec-name">{{ spec.number }}</span>
              <span class="spec-price">¥{{ spec.price.toFixed(2) }}</span>
            </div>
            <div class="spec-details">
              <span v-if="spec.weight">约{{ spec.weight }}g</span>
              <span v-if="spec.length && spec.width">
                约{{ spec.length }}×{{ spec.width }}cm
              </span>
            </div>
            <div class="spec-features" v-if="spec.features">
              <el-tag
                v-for="feature in spec.features"
                :key="feature"
                size="mini"
                class="feature-tag"
              >
                {{ feature }}
              </el-tag>
            </div>
          </div>
        </div>

        <div class="quantity-selector" v-if="selectedSpec">
          <span class="quantity-label">数量：</span>
          <el-input-number
            v-model="quantity"
            :min="1"
            :max="99"
            size="small"
            class="golden-input"
          />
        </div>

        <div class="selected-spec-info" v-if="selectedSpec">
          <div class="total-price">
            总计：<span class="price">¥{{ totalPrice.toFixed(2) }}</span>
          </div>
          <div class="spec-summary">
            已选择：{{ selectedSpec.number }} × {{ quantity }}
          </div>
        </div>
      </div>

      <template #footer>
        <el-button @click="showSpecDialog = false" class="cancel-btn">取消</el-button>
        <el-button
          type="primary"
          :disabled="!selectedSpec"
          @click="addToCartWithSpec"
          class="confirm-btn"
        >
          确认加入购物车
        </el-button>
      </template>
    </el-dialog>
  </el-card>
</template>

<script setup>
import { ref, computed } from 'vue'
import { useCartStore } from '@/store/cart'
import { ElMessageBox, ElMessage } from 'element-plus'
import defaultCake from '@/assets/images/default-cake.jpg'

const emit = defineEmits(['delete', 'add-to-cart'])
const props = defineProps({
  cake: {
    type: Object,
    required: true,
    default: () => ({
      id: '',
      name: '未命名蛋糕',
      image: '',
      tags: [],
      specs: [],
      description: ''
    })
  }
})

const cartStore = useCartStore()
const showSpecDialog = ref(false)
const selectedSpecId = ref(null)
const quantity = ref(1)

// 计算选中的规格
const selectedSpec = computed(() => {
  return props.cake.specs?.find(spec => spec.id === selectedSpecId.value)
})

// 计算总价
const totalPrice = computed(() => {
  if (!selectedSpec.value) return 0
  return selectedSpec.value.price * quantity.value
})

// 计算最低价格
const minPrice = computed(() => {
  if (!props.cake.specs || props.cake.specs.length === 0) {
    return 0
  }
  return Math.min(...props.cake.specs.map(spec => spec.price))
})

// 计算规格数量
const specsCount = computed(() => {
  return props.cake.specs?.length || 0
})

// 处理图片URL
const getImageUrl = (imagePath) => {
  if (!imagePath) return defaultCake
  return imagePath.startsWith('http') ? imagePath 
    : `${imagePath}`
}

// 图片加载失败处理
const handleImageError = (e) => {
  e.target.src = defaultCake
}

// 选择规格
const selectSpec = (spec) => {
  selectedSpecId.value = spec.id
}

// 加入购物车（带规格）
const addToCartWithSpec = async () => {
  if (!selectedSpec.value) {
    ElMessage.warning('请选择规格')
    return
  }

  const itemToCart = {
    productId: props.cake.id,
    name: props.cake.name,
    image: getImageUrl(props.cake.image),
    number: selectedSpec.value.number,
    num: quantity.value,
    amount: selectedSpec.value.price
  }
  
  try {
    await cartStore.addItem(itemToCart)
    ElMessage.success('已加入购物车')
    showSpecDialog.value = false
    selectedSpecId.value = null
    quantity.value = 1
    emit('add-to-cart', itemToCart)
  } catch(error) {
    ElMessage.error("添加购物车失败")
  }
}

// 删除蛋糕
const handleDelete = () => {
  ElMessageBox.confirm(`确定要删除 "${props.cake.name}" 吗？`, '提示', {
    confirmButtonText: '确定',
    cancelButtonText: '取消',
    type: 'warning'
  }).then(() => {
    emit('delete', props.cake.id)
  }).catch(() => {
    // 用户取消删除
  })
}
</script>

<style scoped>
/* 主卡片样式 */
.cake-card {
  border-radius: 16px;
  overflow: hidden;
  margin-bottom: 20px;
  transition: all 0.4s ease;
  box-shadow: 0 4px 20px rgba(214, 187, 112, 0.15);
  border: 1px solid #f5f2eb;
  background: linear-gradient(135deg, #fffdf5, #fff9e6);
  position: relative;
}

.cake-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 40px rgba(214, 187, 112, 0.25);
  border-color: #d6bb70;
}

/* 图片容器 */
.image-container {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 75%;
  overflow: hidden;
  background: linear-gradient(45deg, #f8f4e8, #f5f0e1);
}

.cake-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.5s ease;
}

.cake-card:hover .cake-image {
  transform: scale(1.08);
}

/* 标签样式 */
.tags-container {
  position: absolute;
  top: 12px;
  left: 12px;
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
  z-index: 2;
}

.tag-item {
  background: linear-gradient(45deg, #d6bb70, #b38b2d);
  color: white;
  border: none;
  border-radius: 12px;
  font-weight: bold;
  box-shadow: 0 2px 8px rgba(214, 187, 112, 0.3);
}

/* 商品信息区域 */
.cake-info {
  padding: 20px;
  background: white;
}

/* 名称区域 - 修改后的布局 */
.name-section {
  margin-bottom: 12px;
}

.cake-name {
  margin: 0 0 8px 0;
  font-size: 16px;
  font-weight: 600;
  color: #333;
  line-height: 1.4;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.action-buttons {
  display: flex;
  gap: 8px;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.cake-card:hover .action-buttons {
  opacity: 1;
}

/* 价格区域 */
.price-section {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 16px;
}

.price-main {
  display: flex;
  align-items: baseline;
}

.current-price {
  color: #e4393c;
  font-size: 20px;
  font-weight: bold;
}

.from-text {
  color: #e4393c;
  font-size: 12px;
  margin-left: 4px;
  position: relative;
  top: -1px;
}

.weight {
  color: #d6bb70;
  font-size: 12px;
  font-weight: 500;
}

/* 加入购物车按钮 */
.add-cart-btn {
  width: 100%;
  background: linear-gradient(45deg, #d6bb70, #b38b2d);
  border: none;
  border-radius: 25px;
  padding: 12px;
  color: white;
  font-weight: bold;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(214, 187, 112, 0.3);
}

.add-cart-btn:hover {
  background: linear-gradient(45deg, #b38b2d, #d6bb70);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(214, 187, 112, 0.4);
}

.add-cart-btn i {
  margin-right: 6px;
}

/* 规格对话框样式 */
.spec-dialog-content {
  max-height: 400px;
  overflow-y: auto;
  padding: 10px 0;
}

.spec-list {
  margin-bottom: 20px;
}

.spec-item {
  padding: 16px;
  border: 2px solid #f0f0f0;
  border-radius: 12px;
  margin-bottom: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  background: white;
}

.spec-item:hover {
  border-color: #d6bb70;
  background: #fffdf5;
  transform: translateX(5px);
}

.spec-item.active {
  border-color: #d6bb70;
  background: linear-gradient(135deg, #fffdf5, #f8f4e8);
  box-shadow: 0 4px 15px rgba(214, 187, 112, 0.2);
  position: relative;
}

.spec-item.active::before {
  content: '✓';
  position: absolute;
  top: 50%;
  right: 16px;
  transform: translateY(-50%);
  color: #d6bb70;
  font-weight: bold;
  font-size: 16px;
}

.spec-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.spec-name {
  font-weight: 600;
  color: #333;
  font-size: 15px;
}

.spec-price {
  color: #e4393c;
  font-weight: bold;
  font-size: 16px;
}

.spec-details {
  display: flex;
  gap: 15px;
  font-size: 13px;
  color: #666;
  margin-bottom: 8px;
}

.spec-features {
  display: flex;
  gap: 6px;
  flex-wrap: wrap;
}

.feature-tag {
  background: #f8f4e8;
  color: #d6bb70;
  border: 1px solid #d6bb70;
  border-radius: 10px;
}

.quantity-selector {
  display: flex;
  align-items: center;
  gap: 12px;
  margin: 20px 0;
  padding: 15px;
  background: #f8f4e8;
  border-radius: 10px;
}

.quantity-label {
  font-weight: 500;
  color: #333;
}

:deep(.golden-input .el-input-number__decrease),
:deep(.golden-input .el-input-number__increase) {
  background: #d6bb70;
  color: white;
  border: none;
}

:deep(.golden-input .el-input__inner) {
  border-color: #d6bb70;
}

.selected-spec-info {
  padding: 20px;
  background: linear-gradient(135deg, #f8f4e8, #f5f0e1);
  border-radius: 12px;
  text-align: center;
}

.total-price {
  font-size: 18px;
  margin-bottom: 8px;
}

.price {
  color: #e4393c;
  font-weight: bold;
  font-size: 22px;
}

.spec-summary {
  color: #666;
  font-size: 14px;
}

/* 对话框按钮 */
.cancel-btn {
  border: 1px solid #d6bb70;
  color: #d6bb70;
  border-radius: 20px;
  padding: 10px 20px;
}

.cancel-btn:hover {
  background: #f8f4e8;
  border-color: #b38b2d;
  color: #b38b2d;
}

.confirm-btn {
  background: linear-gradient(45deg, #d6bb70, #b38b2d);
  border: none;
  border-radius: 20px;
  padding: 10px 20px;
  color: white;
}

.confirm-btn:hover {
  background: linear-gradient(45deg, #b38b2d, #d6bb70);
}

.confirm-btn:disabled {
  background: #ccc;
  cursor: not-allowed;
}

/* 对话框样式 */
:deep(.golden-dialog .el-dialog) {
  border-radius: 16px;
  background: linear-gradient(135deg, #fffdf5, #fff9e6);
}

:deep(.golden-dialog .el-dialog__header) {
  background: linear-gradient(45deg, #d6bb70, #b38b2d);
  border-radius: 16px 16px 0 0;
  padding: 20px;
}

:deep(.golden-dialog .el-dialog__title) {
  color: white;
  font-weight: bold;
  font-size: 18px;
}

:deep(.golden-dialog .el-dialog__headerbtn) {
  top: 20px;
}

:deep(.golden-dialog .el-dialog__headerbtn .el-dialog__close) {
  color: white;
  font-size: 18px;
}

/* 强制居中 */
:deep(.el-overlay) {
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  background: rgba(0, 0, 0, 0.5);
}

:deep(.el-dialog) {
  margin: 0 auto !important;
  position: relative !important;
  top: auto !important;
  left: auto !important;
  transform: none !important;
}
</style>