import { defineStore } from 'pinia'
import {getCartItems,addToCart} from '@/api/shoppingCart'

export const useCartStore = defineStore('cart', {
  state: () => ({
    items: [],
    
  }),
  actions: {
    async loadCart() {
      try {
        const res = await getCartItems()
        this.items = res.data
        console.log("购物车：",this.items)
      } catch (error) {
        console.error('加载购物车失败', error)
      }
    },

    async addItem(product) {
      await this.loadCart()
      // 先更新本地
      const existing = this.items.find(item => 
        item.productId === product.productId && item.number === product.number
      )
      if (existing) {
        existing.num += product.num
        product.num = existing.num
        
      } else {
        this.items.push({ ...product })
      }

      // 再发请求
      try {
        await addToCart(product)
      } catch (error) {
        console.error('同步到服务器失败')
        // 可以提示用户“网络异常，已临时保存”
      }
    },
    removeItem(id) {
      this.items = this.items.filter(item => item.id !== id)
    },
    clearCart() {
      this.items = []
    }
  },
  getters: {
    totalItems: (state) => state.items.reduce((total, item) => total + item.quantity, 0),
    totalPrice: (state) => state.items.reduce((total, item) => total + (item.price * item.quantity), 0)
  }
})