<template>
  <el-form 
    ref="formRef" 
    :model="form" 
    label-width="100px"
    label-position="top"
    class="cake-form"
  >
    <!-- 基础信息区域 -->
    <div class="form-section">
      <div class="section-title">蛋糕信息</div>
      <div class="form-grid">
        <el-form-item label="蛋糕名称" prop="name" required>
          <el-input v-model="form.name" placeholder="请输入蛋糕名称" />
        </el-form-item>
        <!-- 口味选择器 -->
        <el-form-item label="口味" prop="tasteId" required>
          <el-select 
            v-model="form.tasteId" 
            placeholder="请选择口味" 
            style="width:100%"
          >
            <el-option
              v-if="form.tasteId && currentTaste"
              :key="currentTaste.id"
              :label="currentTaste.flavor"
              :value="currentTaste.id"
              style="display: none;" 
            />
            <el-option
              v-for="taste in tastes"  
              :key="taste.id"          
              :label="taste.flavor"     
              :value="taste.id"       
            />
          </el-select>
        </el-form-item>
        <el-form-item label="甜度" prop="sweet" required>
          <el-rate
            v-model="form.sweet"
            :max="5"
            :colors="['#E8E8E8', '#F0D9A7', '#d6bb70']"
            :void-color="'#F5F5F5'"
            show-text
            :texts="['1分', '2分', '3分', '4分', '5分']"
            class="sweet-rate"
          />
        </el-form-item>
      </div>
      
      <!-- 描述 -->
      <el-form-item label="描述" prop="description" class="description-item">
        <el-input 
          v-model="form.description" 
          type="textarea" 
          rows="4" 
          placeholder="请输入蛋糕描述信息"
          resize="none"
        />
      </el-form-item>
    </div>
    
    <!-- 商品规格区域 -->
    <div class="form-section spec-section">
      <div class="section-title">商品规格</div>
      <el-table :data="form.specs" border class="spec-table">
        <el-table-column label="规格名称" width="180">
          <template #default="{ row, $index }">
            <el-form-item 
              :prop="`specs.${$index}.number`" 
              :rules="{ required: true, message: '请输入规格名称', trigger: 'blur' }"
              class="table-form-item"
            >
              <el-input v-model="row.number" placeholder="如：2-4人食" />
            </el-form-item>
          </template>
        </el-table-column>
        
        <el-table-column label="价格(元)" width="150">
          <template #default="{ row, $index }">
            <el-form-item 
              :prop="`specs.${$index}.price`" 
              :rules="{ required: true, message: '请输入价格', trigger: 'blur' }"
              class="table-form-item"
            >
              <el-input-number v-model="row.price" :min="0" :precision="2" controls-position="right" />
            </el-form-item>
          </template>
        </el-table-column>
        
        <el-table-column label="重量(g)" width="150">
          <template #default="{ row, $index }">
            <el-form-item 
              :prop="`specs.${$index}.weight`" 
              :rules="{ required: true, message: '请输入重量', trigger: 'blur' }"
              class="table-form-item"
            >
              <el-input-number v-model="row.weight" :min="100" :step="50" controls-position="right" />
            </el-form-item>
          </template>
        </el-table-column>
        
        <el-table-column label="长度(cm)" width="150">
          <template #default="{ row, $index }">
            <el-form-item 
              :prop="`specs.${$index}.length`" 
              :rules="{ required: true, message: '请输入长度', trigger: 'blur' }"
              class="table-form-item"
            >
              <el-input-number v-model="row.length" :min="1" :precision="1" controls-position="right" />
            </el-form-item>
          </template>
        </el-table-column>
        
        <el-table-column label="宽度(cm)" width="150">
          <template #default="{ row, $index }">
            <el-form-item 
              :prop="`specs.${$index}.width`" 
              :rules="{ required: true, message: '请输入宽度', trigger: 'blur' }"
              class="table-form-item"
            >
              <el-input-number v-model="row.width" :min="1" :precision="1" controls-position="right" />
            </el-form-item>
          </template>
        </el-table-column>
        
        <el-table-column label="操作" width="100" align="center">
          <template #default="{ $index }">
            <el-button 
              v-if="form.specs.length > 1"
              type="danger" 
              size="small" 
              :icon="Delete"
              circle
              @click="removeSpec($index)"
            />
          </template>
        </el-table-column>
      </el-table>
      
      <el-button 
        type="primary" 
        :icon="Plus" 
        @click="addSpec"
        class="add-spec-btn"
      >
        添加规格
      </el-button>
    </div>
    
    <!-- 图片上传区域 -->
    <div class="form-section image-section">
      <div class="section-title">蛋糕图片</div>
      <el-form-item class="image-upload-item">
        <el-upload
          action="/api/common/upload"
          :headers="uploadHeaders"
          :data="uploadData"
          :on-success="handleUploadSuccess"
          :on-error="handleUploadError"
          :before-upload="beforeUpload"
          :show-file-list="false"
          accept="image/*"
          class="upload-area"
        >
          <div class="upload-content">
            <el-icon class="upload-icon"><el-icon-picture /></el-icon>
            <div class="upload-text">点击上传蛋糕图片</div>
            <div class="upload-tip">支持JPG/PNG格式，大小不超过2MB</div>
          </div>
        </el-upload>
        
        <div class="image-preview" v-if="form.image">
          <div class="preview-title">当前图片：</div>
          <div class="preview-wrapper">
            <img :src="form.image" alt="预览图片" class="preview-image">
            <el-button 
              class="delete-btn" 
              type="danger" 
              size="small" 
              :icon="Delete" 
              circle 
              @click="removeImage"
            />
          </div>
        </div>
      </el-form-item>
    </div>
    
    <!-- 操作按钮 -->
    <el-form-item class="form-actions">
      <el-button type="primary" @click="submitForm" class="submit-btn">
        {{ isEdit ? '更新蛋糕' : '新增蛋糕' }}
      </el-button>
      <el-button @click="emit('cancel')" class="cancel-btn">取消</el-button>
    </el-form-item>
  </el-form>
</template>

<script setup>
import { ref, watch, computed, onMounted } from 'vue'
import { ElMessage } from 'element-plus'
import { useUserStore } from '@/store/user'
import { Delete, Plus, Picture as ElIconPicture } from '@element-plus/icons-vue'
import { getCakeDetail, getTastes } from '@/api/cake'

// 获取口味列表
const tastes = ref([])
const fetchTastes = async () => {
  try {
    const res = await getTastes()
    tastes.value = res.data
  } catch (error) {
    ElMessage.error('获取口味列表失败')
  }
}

// 在组件创建时获取口味列表
onMounted(fetchTastes)

// 计算当前选中的口味
const currentTaste = computed(() => {
  return tastes.value.find(taste => taste.id === form.value.tasteId)
})

// 添加方法
const setFormData = (data) => {
  form.value = {
    ...INITIAL_FORM_DATA,
    ...data
  };
};

// 暴露方法给父组件
defineExpose({
  setFormData
});

const props = defineProps({
  isEdit: {
    type: Boolean,
    default: true
  },
  cakeId: {
    type: Number,
    default: null
  }
})

const emit = defineEmits(['submit', 'cancel'])

// 表单状态
const formRef = ref(null)
const userStore = useUserStore()

// 初始表单数据
const INITIAL_FORM_DATA = {
  id: null,
  name: '',
  tasteId: null,
  sweet: 3,
  description: '',
  image: '',
  specs: [
    {
      id: null,
      number: '1-2人食',
      price: 198.00,
      length: 15.0,
      width: 15.0,
      weight: 500.0,
    }
  ]
}

// 添加规格
const addSpec = () => {
  form.value.specs.push({
    number: '',
    price: 198.00,
    length: 15.0,
    width: 15.0,
    weight: 500.0,
  })
}

// 删除规格
const removeSpec = (index) => {
  if (form.value.specs.length <= 1) {
    ElMessage.warning('至少保留一个规格')
    return
  }
  form.value.specs.splice(index, 1)
}

// 响应式表单数据
const form = ref({ ...INITIAL_FORM_DATA })

// 加载蛋糕数据
const loadCakeData = async (id) => {
  try {
    // 确保口味数据已加载
    if (tastes.value.length === 0) {
      await fetchTastes();
    }
    const res = await getCakeDetail(id)
    // 确保表单正确设置口味ID
    form.value = {
      ...INITIAL_FORM_DATA,
      ...res.data,
      tasteId: res.data.tasteId
    };
    
    console.log('当前蛋糕数据:', form.value);
    
  } catch (error) {
    ElMessage.error('获取蛋糕详情失败')
    console.error(error)
  }
}

const isEdit = computed(() => props.isEdit);

// 重置表单
const resetForm = () => {
  form.value = { ...INITIAL_FORM_DATA }
}

// 监听cakeId变化
watch(() => props.cakeId, async (id) => {
  if (id) {
    await loadCakeData(id)
  } else {
    resetForm()
  }
}, { immediate: true })

// 提交表单
const submitForm = () => {
  formRef.value.validate(valid => {
    if (valid) {
      // 深拷贝表单数据
      const formData = JSON.parse(JSON.stringify(form.value))
      emit('submit', formData)
    }
  })
}

// 上传相关
const uploadHeaders = computed(() => ({
  'token': `${userStore.token}`
}))

const beforeUpload = (file) => {
  const isImage = file.type.startsWith('image/')
  const isLt2M = file.size / 1024 / 1024 < 2
  
  if (!isImage) {
    ElMessage.error('只能上传图片文件!')
    return false
  }
  if (!isLt2M) {
    ElMessage.error('图片大小不能超过2MB!')
    return false
  }
  
  return true
}

const uploadData = ref({
  type: 'cake'
})

// 上传成功处理
const handleUploadSuccess = (response) => {
  if (response.code === 200) {
    form.value.image = response.data
    ElMessage.success('上传成功')
  } else {
    ElMessage.error(response.message || '上传失败')
  }
}

const handleUploadError = () => {
  ElMessage.error('上传失败，请重试')
}

const removeImage = () => {
  form.value.image = ''
}
</script>

<style scoped>
/* 整体表单样式 */
.cake-form {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(214, 187, 112, 0.1);
  border: 1px solid rgba(214, 187, 112, 0.2);
}

.form-section {
  margin-bottom: 30px;
  padding: 25px;
  border-radius: 10px;
  background: #fefcf6;
  border: 1px solid rgba(214, 187, 112, 0.3);
  transition: all 0.3s ease;
}

.form-section:hover {
  border-color: rgba(214, 187, 112, 0.5);
  box-shadow: 0 2px 12px rgba(214, 187, 112, 0.1);
}

.form-section:last-child {
  margin-bottom: 20px;
}

.section-title {
  font-size: 18px;
  font-weight: 600;
  color: #8B6B3D;
  margin-bottom: 20px;
  padding-bottom: 12px;
  border-bottom: 1px solid rgba(214, 187, 112, 0.3);
  display: flex;
  align-items: center;
}

.section-title::before {
  content: "";
  display: inline-block;
  width: 4px;
  height: 18px;
  background: linear-gradient(to bottom, #d6bb70, #c4a85f);
  border-radius: 2px;
  margin-right: 10px;
}

.form-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  margin-bottom: 15px;
}

.description-item {
  margin-top: 15px;
}

.sweet-rate {
  margin-top: 8px;
}

/* 规格表格样式 */
.spec-section {
  padding: 25px;
}

.spec-table {
  width: 100%;
  margin-bottom: 20px;
  border-radius: 8px;
  overflow: hidden;
  border: 1px solid rgba(214, 187, 112, 0.3);
}

.spec-table :deep(.el-table__header) th {
  background: linear-gradient(to bottom, #f8f4e9, #f1ead5);
  font-weight: 600;
  color: #8B6B3D;
  border-bottom: 1px solid rgba(214, 187, 112, 0.3);
}

.spec-table :deep(.el-table__row) td {
  padding: 12px 0;
  background-color: #fff;
  border-bottom: 1px solid rgba(214, 187, 112, 0.1);
}

.spec-table :deep(.el-table__row:hover td) {
  background-color: #fefaf0;
}

.table-form-item {
  margin-bottom: 0;
}

.table-form-item :deep(.el-form-item__content) {
  margin-left: 0 !important;
}

.add-spec-btn {
  width: 100%;
  padding: 12px;
  font-size: 14px;
  background: linear-gradient(45deg, #d6bb70, #c4a85f);
  border: none;
  border-radius: 6px;
  color: white;
  transition: all 0.3s;
  font-weight: 500;
}

.add-spec-btn:hover {
  background: linear-gradient(45deg, #c4a85f, #b3984e);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(214, 187, 112, 0.4);
}

/* 图片上传样式 */
.image-section {
  padding: 25px;
}

.image-upload-item {
  margin-bottom: 0;
}

.upload-area {
  border: 2px dashed rgba(214, 187, 112, 0.5);
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s;
  background-color: #fefcf6;
  height: 180px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.upload-area:hover {
  border-color: #d6bb70;
  background-color: #fdf8ec;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(214, 187, 112, 0.2);
}

.upload-content {
  text-align: center;
  padding: 20px;
}

.upload-icon {
  font-size: 48px;
  color: #d6bb70;
  margin-bottom: 12px;
}

.upload-text {
  font-size: 16px;
  font-weight: 500;
  color: #8B6B3D;
  margin-bottom: 6px;
}

.upload-tip {
  font-size: 13px;
  color: #a8915e;
}

.image-preview {
  margin-top: 25px;
}

.preview-title {
  font-size: 14px;
  color: #8B6B3D;
  margin-bottom: 10px;
  font-weight: 500;
}

.preview-wrapper {
  position: relative;
  display: inline-block;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 12px rgba(214, 187, 112, 0.3);
  border: 2px solid rgba(214, 187, 112, 0.3);
  transition: all 0.3s;
}

.preview-wrapper:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(214, 187, 112, 0.4);
}

.preview-image {
  display: block;
  width: 200px;
  height: 200px;
  object-fit: cover;
  border-radius: 8px;
}

.delete-btn {
  position: absolute;
  top: 8px;
  right: 8px;
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(4px);
  border: 1px solid rgba(214, 187, 112, 0.3);
}

.delete-btn:hover {
  background: #fff;
  transform: scale(1.1);
}

/* 操作按钮样式 */
.form-actions {
  display: flex;
  justify-content: center;
  margin-top: 30px;
  gap: 20px;
  padding-top: 20px;
  border-top: 1px solid rgba(214, 187, 112, 0.3);
}

.submit-btn {
  padding: 12px 40px;
  font-size: 16px;
  background: linear-gradient(45deg, #d6bb70, #c4a85f);
  border: none;
  border-radius: 8px;
  transition: all 0.3s;
  color: white;
  font-weight: 500;
  letter-spacing: 1px;
}

.submit-btn:hover {
  background: linear-gradient(45deg, #c4a85f, #b3984e);
  transform: translateY(-3px);
  box-shadow: 0 6px 20px rgba(214, 187, 112, 0.4);
}

.cancel-btn {
  padding: 12px 40px;
  font-size: 16px;
  border-radius: 8px;
  background: #fefcf6;
  color: #8B6B3D;
  border: 1px solid rgba(214, 187, 112, 0.5);
  transition: all 0.3s;
  font-weight: 500;
}

.cancel-btn:hover {
  background: rgba(214, 187, 112, 0.1);
  color: #745c2d;
  border-color: #d6bb70;
  transform: translateY(-2px);
}

/* 表单元素样式优化 */
:deep(.el-input__wrapper) {
  border-radius: 6px;
  transition: all 0.3s;
}

:deep(.el-input__wrapper:hover) {
  border-color: #d6bb70;
  box-shadow: 0 0 0 1px rgba(214, 187, 112, 0.3);
}

:deep(.el-input__wrapper.is-focus) {
  border-color: #d6bb70;
  box-shadow: 0 0 0 1px rgba(214, 187, 112, 0.5);
}

:deep(.el-select .el-input__wrapper) {
  border-radius: 6px;
}

:deep(.el-textarea__inner) {
  border-radius: 6px;
  transition: all 0.3s;
}

:deep(.el-textarea__inner:hover) {
  border-color: #d6bb70;
}

:deep(.el-textarea__inner:focus) {
  border-color: #d6bb70;
  box-shadow: 0 0 0 1px rgba(214, 187, 112, 0.3);
}

/* 响应式调整 */
@media (max-width: 992px) {
  .form-grid {
    grid-template-columns: 1fr;
  }
  
  .spec-table {
    overflow-x: auto;
  }
  
  .spec-table :deep(.el-table) {
    min-width: 900px;
  }
}

@media (max-width: 768px) {
  .cake-form {
    padding: 15px;
  }
  
  .form-section {
    padding: 20px 15px;
  }
  
  .form-actions {
    flex-direction: column;
    gap: 15px;
  }
  
  .submit-btn, .cancel-btn {
    width: 100%;
    padding: 12px;
  }
  
  .spec-section, .image-section {
    padding: 20px 15px;
  }
}
</style>