<template>
  <div class="dessert-container">
    <!-- 顶部轮播图 -->
    <div class="banner-section">
      <el-carousel height="400px" indicator-position="outside" arrow="always">
        <el-carousel-item v-for="(item, index) in banners" :key="index">
          <img :src="item.image" class="banner-image" />
          <div class="banner-overlay">
            <div class="banner-text">
              <h2>{{ item.title }}</h2>
              <p>{{ item.subtitle }}</p>
              
            </div>
          </div>
        </el-carousel-item>
      </el-carousel>
    </div>

    <!-- 甜品展示区域 -->
    <div class="dessert-section">
      <!-- 区域标题 -->
      <div class="section-header">
        <div class="title-wrapper">
          <h2 class="section-title">精选甜品</h2>
          <div class="title-decoration">
            <span class="decoration-line"></span>
            <i class="decoration-icon">✨</i>
            <span class="decoration-line"></span>
          </div>
          <p class="section-subtitle">每一口都是奢华的享受</p>
        </div>
      </div>
      
      <!-- 加载状态 -->
      <div v-if="loading" class="loading-container">
        <el-skeleton :rows="6" animated />
      </div>

      <!-- 蛋糕列表 -->
      <div v-else class="cake-list">
        <el-row :gutter="24">
          <el-col 
            v-for="cake in desserts" 
            :key="cake.id"
            :xs="24" :sm="12" :md="8" :lg="6"
          >
            <cake-card :cake="cake" @click="goToDetail(cake.id)">
              <template #actions>
                <el-button 
                  size="mini" 
                  type="danger"
                  class="delete-btn"
                  @click.stop="handleDelete(cake.id)"
                >
                  删除
                </el-button>
                <el-button 
                  size="mini" 
                  class="edit-btn"
                  @click.stop="handleEdit(cake)"
                >
                  编辑
                </el-button>
              </template>
            </cake-card>
          </el-col>
        </el-row>
        
        <!-- 空状态 -->
        <el-empty v-if="desserts.length === 0" description="暂无蛋糕商品" />
      </div>

      <!-- 添加新甜品的卡片 -->
      <div class="dessert-card add-card" @click="handleAddDessert">
        <div class="add-card-content">
          <el-icon class="add-icon"><Plus /></el-icon>
          <span class="add-text">添加新甜品</span>
          <p class="add-tip">打造您的专属甜品</p>
        </div>
      </div>
    </div>

    <!-- 分页组件 -->
    <!-- <div class="pagination-container">
      <div class="pagination-wrapper">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="pagination.pageNum"
          :page-size="pagination.pageSize"
          layout="prev, pager, next"
          :total="pagination.total"
          prev-text="上一页"
          next-text="下一页"
        />
        <div class="total-pages">
          共 {{ Math.ceil(pagination.total / pagination.pageSize) }} 页
        </div>
      </div>
    </div> -->

    <!-- 添加/编辑对话框 -->
    <el-dialog
      v-model="showAddDialog"
      :title="editing ? '编辑甜品' : '添加新甜品'"
      width="600px"
      destroy-on-close
      :modal="false"
      :lock-scroll="false"
      :append-to-body="true"
      class="golden-dialog"
      @closed="resetForm"
    >
      <!-- 使用抽离出的表单组件 -->
      <DessertForm
        ref="dessertFormRef"
        v-model="currentDessert"
        :upload-headers="uploadHeaders"
        @upload-success="(url) => (currentDessert.image = url)"
        @remove-image="() => (currentDessert.image = '')"
      />

      <template #footer>
        <el-button @click="showAddDialog = false" class="cancel-btn">取消</el-button>
        <el-button type="primary" @click="submitForm" :loading="submitting" class="confirm-btn">
          {{ editing ? '保存修改' : '确认添加' }}
        </el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, reactive, onMounted, watch, computed, nextTick } from 'vue'
import { Plus} from '@element-plus/icons-vue'
import { ElMessage, ElMessageBox, ElLoading } from 'element-plus'
import { 
  getDesserts, 
  addDessert, 
  updateDessert, 
  deleteDessert,
  saveDessert 
} from '@/api/dessert'
import { useUserStore } from '@/store/user'
import router from '@/router'
import CakeCard from '@/components/CakeCard.vue'
import DessertForm from '@/components/DessertForm.vue'

// 轮播图数据
const banners = ref([
  {
    title: '金色奢华 甜蜜盛宴',
    subtitle: '精选顶级原料，匠心制作',
    image: 'https://lsl-bucket.oss-cn-guangzhou.aliyuncs.com/911fff0a-0151-42dd-ba7e-2f82e277b91f.jpg'
  },
  {
    title: '精选原料 品质保证',
    subtitle: '严选全球优质食材，只为极致口感',
    image: 'https://lsl-bucket.oss-cn-guangzhou.aliyuncs.com/2b5e0020-f5be-4611-bf47-bbd875979e13.jpg'
  },
  {
    title: '贵族享受 品质之选',
    subtitle: '全国冷链配送，新鲜直达',
    image: 'https://lsl-bucket.oss-cn-guangzhou.aliyuncs.com/2b628d88-6ace-4ae0-8ebe-23bdbfa26c76.jpg'
  }
])

const dessertFormRef = ref(null)
const desserts = ref([])
const loading = ref(false)
const userStore = useUserStore()

// 上传相关逻辑
const uploadHeaders = computed(() => ({
  'token': `${userStore.token}`
}))

const resetForm = () => {
  Object.assign(currentDessert, {
    id: null,
    name: '',
    tasteId: null,
    categoryId: null,
    image: '',
    sweet: 3,
    description: '',
    specs: []
  })

  nextTick(() => {
    if (dessertFormRef.value) {
      dessertFormRef.value.clearValidate()
    }
  })
}

// 编辑甜品
const handleEdit = (dessert) => {
  Object.assign(currentDessert, {
    id: dessert.id,
    name: dessert.name,
    tasteId: dessert.tasteId,
    categoryId: dessert.categoryId,
    image: dessert.image,
    sweet: dessert.sweet,
    description: dessert.description,
    specs: dessert.specs.map(spec => ({
      id: spec.id,
      number: spec.number,
      price: spec.price,
      length: spec.length,
      width: spec.width,
      weight: spec.weight
    }))
  })
  
  editing.value = true
  showAddDialog.value = true
}

// 跳转商品详情页
const goToDetail = (id) => {
  router.push(`/cake/${id}`)
}

const handleAddDessert = () => {
  editing.value = false
  resetForm()
  showAddDialog.value = true
  
  if (currentDessert.specs.length === 0) {
    addSpec()
  }
}

// 删除甜品
const handleDelete = async (id) => {
  try {
    await ElMessageBox.confirm('确定要删除这个甜品吗?', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    })
    
    const loadingInstance = ElLoading.service({
      lock: true,
      text: '正在删除...',
      background: 'rgba(255, 255, 255, 0.7)'
    })
    
    try {
      await deleteDessert(id)
      ElMessage.success('删除成功')
      if (desserts.value.length === 1 && pagination.pageNum > 1) {
        pagination.pageNum -= 1
      }
      await loadDesserts()
    } finally {
      loadingInstance.close()
    }
  } catch (error) {
    if (error !== 'cancel') {
      ElMessage.error('删除失败')
      console.error(error)
    }
  }
}

// 对话框相关
const showAddDialog = ref(false)
watch(showAddDialog, (val) => {
  if (val) {
    nextTick(() => {
      const dialogBody = document.querySelector('.el-dialog__body')
      if (dialogBody) dialogBody.scrollTop = 0
    })
  }
})

const editing = ref(false)
const currentDessert = reactive({
  id: null,
  name: '',
  tasteId: null,
  categoryId: null,
  image: '',
  sweet: 3,
  description: '',
  specs: []
})

// 分页相关数据
const pagination = reactive({
  pageNum: 1,
  pageSize: 8,
  total: 0
})

const loadDesserts = async () => {

  
  try {
    const params = {
      pageNum: pagination.pageNum,
      pageSize: pagination.pageSize
    }
    const response = await getDesserts(params)
    
    desserts.value = response.data.records
    console.log("甜品信息：", desserts.value)
    pagination.total = response.data.total
  } catch (error) {
    console.error('加载甜品失败:', error)
  } 
}

const handleCurrentChange = (newPage) => {
  pagination.pageNum = newPage
  loadDesserts()
}

const submitForm = async () => {
  try {
    await dessertFormRef.value.validate()
    
    const loadingInstance = ElLoading.service({
      lock: true,
      text: '正在保存...',
      background: 'rgba(255, 255, 255, 0.7)'
    })
    
    try {
      const dessertData = {
        ...currentDessert,
        specs: currentDessert.specs.map(spec => ({
          ...spec,
          price: Number(spec.price),
          length: Number(spec.length),
          width: Number(spec.width),
          weight: Number(spec.weight)
        }))
      }

      if (editing.value) {
        await updateDessert(dessertData)
        ElMessage.success('更新成功')
      } else {
        await addDessert(dessertData)
        ElMessage.success('添加成功')
      }

      showAddDialog.value = false
      await loadDesserts()
    } finally {
      loadingInstance.close()
    }
  } catch (error) {
    ElMessage.error(error.message || '操作失败')
  }
}

const addSpec = () => {
  currentDessert.specs.push({
    id: null,
    productId: null,
    number: '',
    price: 0,
    length: null,
    width: null,
    weight: null
  })
}

// 页面加载时自动获取数据
onMounted(() => {
  loadDesserts()
})
</script>

<style scoped>
/* 金色奢华主题全局样式 */
.dessert-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0;
  font-family: 'PingFang SC', 'Microsoft YaHei', sans-serif;
  background: linear-gradient(135deg, #fffdf5 0%, #fff9e6 100%);
  min-height: 100vh;
}

/* 轮播图金色主题 */
.banner-section {
  position: relative;
  border-radius: 0 0 20px 20px;
  overflow: hidden;
  box-shadow: 0 8px 32px rgba(214, 187, 112, 0.2);
  margin-bottom: 40px;
}

.banner-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.8s ease;
}

.el-carousel-item:hover .banner-image {
  transform: scale(1.05);
}

.banner-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    45deg,
    rgba(214, 187, 112, 0.3) 0%,
    rgba(179, 139, 45, 0.2) 100%
  );
  display: flex;
  align-items: center;
  justify-content: center;
}

.banner-text {
  text-align: center;
  color: white;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

.banner-text h2 {
  font-size: 36px;
  margin-bottom: 12px;
  font-weight: 600;
  letter-spacing: 1px;
}

.banner-text p {
  font-size: 18px;
  margin-bottom: 24px;
  opacity: 0.95;
}

.banner-btn {
  background: linear-gradient(45deg, #d6bb70, #b38b2d);
  border: none;
  border-radius: 25px;
  padding: 12px 32px;
  font-size: 16px;
  font-weight: 500;
  color: white;
  box-shadow: 0 4px 15px rgba(214, 187, 112, 0.4);
  transition: all 0.3s ease;
}

.banner-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(214, 187, 112, 0.6);
}

/* 区域标题 */
.section-header {
  text-align: center;
  margin-bottom: 40px;
  padding: 0 20px;
}

.title-wrapper {
  position: relative;
}

.section-title {
  font-size: 32px;
  font-weight: 600;
  color: #b38b2d;
  margin-bottom: 16px;
  position: relative;
}

.title-decoration {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 16px;
  margin-bottom: 12px;
}

.decoration-line {
  width: 60px;
  height: 2px;
  background: linear-gradient(90deg, transparent, #d6bb70, transparent);
}

.decoration-icon {
  color: #d6bb70;
  font-size: 18px;
}

.section-subtitle {
  font-size: 16px;
  color: #8a7c5a;
  font-style: italic;
}

/* 甜品展示区域 */
.dessert-section {
  padding: 0 20px 40px;
}

.cake-list {
  margin-bottom: 40px;
}

.delete-btn {
  background: #fdf6f0;
  color: #e74c3c;
  border: none;
  border-radius: 15px;
  transition: all 0.3s;
  margin-right: 8px;
}

.delete-btn:hover {
  background: #fadbd8;
  color: #c0392b;
}

/* 编辑按钮 */
.edit-btn {
  background: #f5ecd8;
  color: #b38b2d;
  border: none;
  border-radius: 15px;
  transition: all 0.3s;
}

.edit-btn:hover {
  background: #d6bb70;
  color: white;
}

/* 添加卡片金色主题 */
.add-card {
  background: linear-gradient(135deg, #fffdf5, #f8f4e8);
  border: 2px dashed #d6bb70;
  border-radius: 16px;
  padding: 40px 20px;
  text-align: center;
  cursor: pointer;
  transition: all 0.4s ease;
  margin: 20px auto;
  max-width: 300px;
}

.add-card:hover {
  border-color: #b38b2d;
  background: linear-gradient(135deg, #f8f4e8, #f5f0e1);
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(214, 187, 112, 0.2);
}

.add-card-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
}

.add-icon {
  font-size: 48px;
  color: #d6bb70;
  transition: all 0.3s ease;
}

.add-card:hover .add-icon {
  color: #b38b2d;
  transform: scale(1.1);
}

.add-text {
  font-size: 18px;
  font-weight: 600;
  color: #b38b2d;
}

.add-tip {
  font-size: 14px;
  color: #8a7c5a;
  margin: 0;
}

/* 分页样式 - 金色风格 */
.pagination-container {
  margin-top: 30px;
  text-align: center;
}
.pagination-wrapper {
  display: inline-flex;
  align-items: center;
  background: #f5ecd8;
  border-radius: 20px;
  padding: 5px 10px;
}

.total-pages {
  margin-left: 15px;
  color: #b38b2d;
  font-weight: bold;
  font-size: 14px;
}


.pagination-container ::v-deep .el-pagination {
  display: inline-flex;
  align-items: center;
}

.pagination-container ::v-deep .btn-prev,
.pagination-container ::v-deep .btn-next {
  padding: 0 15px;
  border-radius: 15px;
  background: #f5ecd8;
  color: #b38b2d;
  border: none;
  margin: 0 5px;
}

.pagination-container ::v-deep .el-pager li {
  border-radius: 15px;
  background: #f5ecd8;
  margin: 0 5px;
  color: #b38b2d;
}

.pagination-container ::v-deep .el-pager li.active {
  background: linear-gradient(45deg, #d6bb70, #b38b2d);
  color: white;
  font-weight: bold;
}

/* 对话框金色主题 */
:deep(.golden-dialog .el-dialog) {
  border-radius: 20px;
  background: linear-gradient(135deg, #fffdf5, #fff9e6);
  border: 1px solid #e8dfc8;
}

:deep(.golden-dialog .el-dialog__header) {
  background: linear-gradient(45deg, #d6bb70, #b38b2d);
  border-radius: 20px 20px 0 0;
  padding: 24px;
}

:deep(.golden-dialog .el-dialog__title) {
  color: white;
  font-weight: 600;
  font-size: 20px;
}

:deep(.golden-dialog .el-dialog__headerbtn) {
  top: 24px;
}

:deep(.golden-dialog .el-dialog__headerbtn .el-dialog__close) {
  color: white;
  font-size: 20px;
}

/* 对话框按钮 */
.cancel-btn {
  border: 1px solid #d6bb70;
  color: #d6bb70;
  border-radius: 20px;
  padding: 10px 24px;
  transition: all 0.3s ease;
}

.cancel-btn:hover {
  background: #f8f4e8;
  border-color: #b38b2d;
  color: #b38b2d;
}

.confirm-btn {
  background: linear-gradient(45deg, #d6bb70, #b38b2d);
  border: none;
  border-radius: 20px;
  padding: 10px 24px;
  color: white;
  transition: all 0.3s ease;
}

.confirm-btn:hover {
  background: linear-gradient(45deg, #b38b2d, #d6bb70);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(214, 187, 112, 0.3);
}

/* 加载状态金色主题 */
.loading-container {
  padding: 40px;
  background: white;
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(214, 187, 112, 0.1);
  margin: 20px 0;
}

/* 空状态金色主题 */
:deep(.el-empty) {
  padding: 60px 0;
}

:deep(.el-empty__description p) {
  color: #8a7c5a;
}

/* 响应式设计 */
@media (max-width: 1200px) {
  .dessert-container {
    max-width: 100%;
    padding: 0 10px;
  }
}

@media (max-width: 768px) {
  .banner-text h2 {
    font-size: 28px;
  }
  
  .banner-text p {
    font-size: 16px;
  }
  
  .section-title {
    font-size: 28px;
  }
  
  .dessert-section {
    padding: 0 15px 30px;
  }
  
  .pagination-wrapper {
    flex-direction: column;
    gap: 12px;
  }
  
  .total-pages {
    margin-left: 0;
  }
}

@media (max-width: 576px) {
  .banner-section {
    margin-bottom: 30px;
  }
  
  .banner-text h2 {
    font-size: 24px;
  }
  
  .section-title {
    font-size: 24px;
  }
  
  .add-card {
    max-width: 100%;
    margin: 20px 0;
  }
}
</style>