import request from '@/utils/request';

export function pageQuery(params){
    return request({
        url: '/pet/pageQuery',
        method: 'get',
        params: params
    })
}

export function getCakeById(id){
  return request({
    url: `/pet/get/${id}`,
    method: 'get',
  })
}

export function addPetCake(data){
  return request({
    url: '/pet/add',
    method: 'put',
    data: data
  })
}

export function updatePetCake(data){
  return request({
    url: '/pet/update',
    method: 'post',
    data: data
  })
}

export function saveSpecs(productId,specs){
  return request({
    url: `/update/${productId}`,
    method: 'post',
    data: specs
  })
}

export function deletePetCake(id){
  return request({
    url: `/pet/delete/${id}`,
    method:'delete',
  })
}