import request from '@/utils/request';

export function getDesserts(params){
    return request({
        url: '/desserts/list',
        method: 'get',
        params: params
    })
}

export function updateDessert(data){
    return request({
        url: '/desserts/update',
        method: 'post',
        data: data
    })
}

export function addDessert(data){
    return request({
        url: '/desserts/add',
        method: 'put',
        data: data
    }) 
}

export function deleteDessert(id){
    return request({
        url: `/desserts/delete/${id}`,
        method: 'delete'
    })
}