<!-- src/components/AddressForm.vue -->
<template>
  <el-form :model="form" :rules="rules" ref="formRef" label-width="80px" :size="size">
    <el-form-item label="收货人" prop="consignee">
      <el-input v-model="form.consignee" placeholder="请输入收货人姓名" />
    </el-form-item>
    <el-form-item label="性别" prop="sex">
      <el-select v-model="form.sex" placeholder="请选择性别">
        <el-option label="男" value="男" />
        <el-option label="女" value="女" />
      </el-select>
    </el-form-item>
    <el-form-item label="手机号" prop="phone">
      <el-input v-model="form.phone" placeholder="请输入手机号" />
    </el-form-item>
    <el-form-item label="所在地区" prop="region">
      <el-cascader
        v-model="form.region"
        :options="regionOptions"
        placeholder="请选择省/市/区"
        style="width: 100%;"
      />
    </el-form-item>
    <el-form-item label="详细地址" prop="detail">
      <el-input v-model="form.detail" type="textarea" :rows="2" placeholder="请输入详细地址" />
    </el-form-item>
    <el-form-item label="设为默认" prop="isDefault">
      <el-switch
        v-model="form.isDefault"
        :active-value="1"
        :inactive-value="0"
      />
    </el-form-item>
  </el-form>
</template>

<script setup>
import { listRegion } from '@/api/region'
import { ref, reactive, watch } from 'vue'

// 接收 props
const props = defineProps({
  address: {
    type: Object,
    default: null
  },
  size: {
    type: String,
    default: 'default'
  }
})

// 暴露给父组件的方法
const emit = defineEmits(['submit', 'cancel'])

// 表单数据
const form = reactive({
  id: null,
  consignee: '',
  sex: '男',
  phone: '',
  region: [], // 级联选择值 [provinceCode, cityCode, districtCode]
  detail: '',
  isDefault: 0,
  // 下面是用于显示的 name 字段（实际不提交）
  provinceName: '',
  cityName: '',
  districtName: ''
})

// 重置表单
const resetForm = () => {
  form.id = null
  form.consignee = ''
  form.sex = '男'
  form.phone = ''
  form.region = []
  form.detail = ''
  form.isDefault = 0
  form.provinceName = ''
  form.cityName = ''
  form.districtName = ''
}



// 表单验证规则
const rules = {
  consignee: [{ required: true, message: '请输入收货人姓名', trigger: 'blur' }],
  sex: [{ required: false, message: '请输入性别', trigger: 'change' }],
  phone: [
    { required: true, message: '请输入手机号', trigger: 'blur' },
    { pattern: /^1[3-9]\d{9}$/, message: '手机号格式错误', trigger: 'blur' }
  ],
  region: [{ type: 'array', required: true, message: '请选择地区', trigger: 'change' }],
  detail: [{ required: true, message: '请输入详细地址', trigger: 'blur' }]
}

// 省市区数据（实际项目中应从 API 或全局 store 获取）
const regionOptions = ref([])

// 表单引用
const formRef = ref()

// 监听 address 变化，用于编辑
watch(() => props.address, (newVal) => {
  if (newVal) {
    form.id = newVal.id
    form.consignee = newVal.consignee
    form.sex = newVal.sex
    form.phone = newVal.phone
    form.detail = newVal.detail
    form.isDefault = newVal.isDefault || 0

    // 设置省市区 code 数组
    const codes = [newVal.provinceCode, newVal.cityCode, newVal.districtCode]
    form.region = codes.filter(Boolean)

    // 同步 name 字段（用于显示）
    form.provinceName = newVal.provinceName || ''
    form.cityName = newVal.cityName || ''
    form.districtName = newVal.districtName || ''
  } else {
    resetForm()
  }
}, { immediate: true })

// 提交表单
const submit = async () => {
  try {
    await formRef.value.validate()
    // 格式化提交数据
    const [provinceCode, cityCode, districtCode] = form.region
    const submitData = {
      id: form.id,
      userId: form.userId, // 若需填写，请补充
      consignee: form.consignee,
      sex: form.sex,
      phone: form.phone,
      provinceCode: provinceCode,
      provinceName: getRegionLabel(provinceCode),
      cityCode: cityCode,
      cityName: getRegionLabel(cityCode),
      districtCode: districtCode,
      districtName: getRegionLabel(districtCode),
      detail: form.detail,
      isDefault: form.isDefault,
      createTime: undefined, // 不需要前端设置
      updateTime: undefined // 不需要前端设置
    }
    emit('submit', submitData)
  } catch (error) {
    console.error('表单验证失败', error)
  }
}

// 取消操作
const cancel = async() => {
  emit('cancel')
  await resetForm()
}

// 暴露方法给父组件
defineExpose({
  submit,
  cancel,
  resetForm
})

const getRegionLabel = (code) => {
  if (!code) return ''

  const findLabel = (options, targetCode) => {
    for (const item of options) {
      if (item.value === targetCode) return item.label
      if (item.children && item.children.length > 0) {
        const result = findLabel(item.children, targetCode)
        if (result) return result
      }
    }
    return ''
  }

  return findLabel(regionOptions.value, code)
}

// 初始化：加载省市区数据
const loadRegionData = async () => {
  try {
    const response = await listRegion()
    const regions = response.data // 假设后端返回的是 List<Region> 数组

   console.log("地区信息：",regions)
    const options = convertToCascaderFormat(regions)
    regionOptions.value = options
     console.log("地区信息2：",regionOptions.value)
  } catch (error) {
    console.error('加载地区数据失败:', error)
    // 可以设置默认值或提示用户
  }
}
function convertToCascaderFormat(data) {
  return data.map(item => ({
    value: item.code,
    label: item.name,
    children: item.children ? convertToCascaderFormat(item.children) : []
  }));
}

// 页面加载时自动加载数据
loadRegionData()
</script>