import request from "@/utils/request";

export function getUserInfo(){
    return request({
        url: "getUserInfo",
        method:'get'
    })
}

export function updateUser(name){
    return request({
        url: `/updateName/${name}`,
        method:"post",
    })
}

export function changePassword(data){
    return request({
        url: '/updatePassword',
        method: 'post',
        data: data
    })
}