<!-- src/components/PersonalCenter.vue -->
<template>
  <div class="personal-center">
    <div class="page-header">
      <h2>个人中心</h2>
      <p class="page-subtitle">管理您的账户信息和收货地址</p>
    </div>

    <!-- 用户信息部分 -->
    <el-card class="user-card" shadow="hover">
      <template #header>
        <div class="card-header">
          <span class="card-title">账户信息</span>
          <el-icon class="card-icon"><User /></el-icon>
        </div>
      </template>
      
      <div class="user-info">
        <div class="user-avatar">
          <el-avatar :size="100" :src="userInfo.avatar" />
          <div class="user-name">{{ userInfo.name }}</div>
        </div>
        <div class="user-details">
          <div class="info-item">
            <div class="label">
              <el-icon><User /></el-icon>
              <span>用户名</span>
            </div>
            <div class="value">{{ userInfo.name }}</div>
          </div>
          <div class="info-item">
            <div class="label">
              <el-icon><Wallet /></el-icon>
              <span>账户余额</span>
            </div>
            <div class="value amount">{{ userInfo.amount }}</div>
          </div>
          <div class="action-buttons">
            <el-button type="primary" class="action-btn" @click="showChangeNameDialog">
              <el-icon><Edit /></el-icon>
              修改用户名
            </el-button>
            <el-button class="action-btn secondary" @click="showChangePasswordDialog">
              <el-icon><Lock /></el-icon>
              修改密码
            </el-button>
          </div>
        </div>
      </div>
    </el-card>

    <!-- 地址管理部分 -->
    <el-card class="address-card" shadow="hover">
      <template #header>
        <div class="card-header">
          <div class="header-left">
            <span class="card-title">收货地址管理</span>
            <el-icon class="card-icon"><Location /></el-icon>
          </div>
          <div class="header-right">
            <el-button type="primary" class="add-address-btn" @click="showAddressDialog('add')">
              <el-icon><Plus /></el-icon>
              新增地址
            </el-button>
          </div>
        </div>
      </template>

      <!-- 地址列表 - 卡片视图 -->
      <div class="address-list" v-loading="loading">
        <div 
          v-for="address in addressList" 
          :key="address.id" 
          class="address-item"
          :class="{ 'default-address': address.isDefault === 1 }"
        >
          <!-- 地址卡片内容保持不变 -->
          <div class="address-header">
            <div class="address-consignee">
              <span>{{ address.consignee }}</span>
              <el-tag v-if="address.isDefault === 1" class="default-tag">默认地址</el-tag>
            </div>
            <div class="address-phone">
              <el-icon><Phone /></el-icon>
              {{ address.phone }}
            </div>
          </div>
          
          <div class="address-content">
            <div class="address-region">
              <el-icon><Location /></el-icon>
              {{ address.regionName }}
            </div>
            <div class="address-detail">
              {{ address.detail }}
            </div>
          </div>
          
          <div class="address-actions">
            <el-button 
              size="small" 
              class="address-action-btn"
              @click="showAddressDialog('edit', address)"
            >
              <el-icon><Edit /></el-icon>
              编辑
            </el-button>
            <el-button 
              size="small" 
              class="address-action-btn danger"
              @click="deleteAddress(address.id)"
            >
              <el-icon><Delete /></el-icon>
              删除
            </el-button>
            <el-button 
              v-if="address.isDefault !== 1"
              size="small" 
              class="address-action-btn secondary"
              @click="setDefaultAddress(address.id)"
            >
              <el-icon><Star /></el-icon>
              设为默认
            </el-button>
          </div>
        </div>
        
        <div v-if="addressList.length === 0 && !loading" class="empty-address">
          <el-empty description="暂无收货地址" :image-size="150">
            <el-button type="primary" @click="showAddressDialog('add')">添加地址</el-button>
          </el-empty>
        </div>
      </div>
    </el-card>

    <!-- 修改用户名弹窗 -->
    <el-dialog
      v-model="nameDialogVisible"
      title="修改用户名"
      width="500px"
      @close="closeNameDialog"
    >
      <el-form
        :model="nameForm"
        :rules="nameRules"
        ref="nameFormRef"
        label-width="100px"
      >
        <el-form-item label="新用户名" prop="newName">
          <el-input
            v-model="nameForm.newName"
            placeholder="请输入新用户名"
          />
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="closeNameDialog">取消</el-button>
          <el-button type="primary" @click="handleChangeName">确定</el-button>
        </span>
      </template>
    </el-dialog>

    <!-- 修改密码弹窗 -->
    <el-dialog
      v-model="passwordDialogVisible"
      title="修改密码"
      width="500px"
      @close="closePasswordDialog"
    >
      <el-form
        :model="passwordForm"
        :rules="passwordRules"
        ref="passwordFormRef"
        label-width="100px"
      >
        <el-form-item label="原密码" prop="oldPassword">
          <el-input
            v-model="passwordForm.oldPassword"
            type="password"
            placeholder="请输入原密码"
            show-password
          />
        </el-form-item>
        <el-form-item label="新密码" prop="newPassword">
          <el-input
            v-model="passwordForm.newPassword"
            type="password"
            placeholder="请输入新密码"
            show-password
          />
        </el-form-item>
        <el-form-item label="确认密码" prop="confirmPassword">
          <el-input
            v-model="passwordForm.confirmPassword"
            type="password"
            placeholder="请再次输入新密码"
            show-password
          />
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="closePasswordDialog">取消</el-button>
          <el-button type="primary" @click="handleChangePassword">确定</el-button>
        </span>
      </template>
    </el-dialog>

    <!-- 地址表单弹窗 -->
    <el-dialog
      v-model="addressDialogVisible"
      :title="addressDialogTitle"
      width="600px"
      @close="closeAddressDialog"
    >
      <AddressForm
        ref="addressFormRef"  
        :address="currentAddress"
        :visible="addressDialogVisible"
        @submit="handleAddressSubmit"
        @cancel="closeAddressDialog"
      />

      <template #footer>
        <span class="dialog-footer">
          <el-button @click="closeAddressDialog">取消</el-button>
          <el-button type="primary" @click="handleAddressFormSubmit">确定</el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, reactive, onMounted, computed } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { 
  User, 
  Wallet, 
  Edit, 
  Lock, 
  Location, 
  Plus, 
  Phone, 
  Delete, 
  Star 
} from '@element-plus/icons-vue'
import AddressForm from '@/components/AddressForm.vue'
import { getAddressList, delAddress, updateAddress, addAddress } from '@/api/address'
import { getUserInfo, updateUser, changePassword } from '@/api/user'

// ======================== 数据状态 ========================
const userInfo = reactive({
  name: '',
  amount: '0.00',
  avatar: 'https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png' // 默认头像
})

const addressList = ref([])
const loading = ref(false)
const currentAddress = ref(null) // 当前编辑或添加的地址

// 弹窗状态
const nameDialogVisible = ref(false)
const passwordDialogVisible = ref(false)
const addressDialogVisible = ref(false)
const dialogType = ref('add') // 'add' 或 'edit'
const addressFormRef = ref(null)

// 用户名表单
const nameForm = reactive({
  newName: ''
})

// 密码表单
const passwordForm = reactive({
  oldPassword: '',
  newPassword: '',
  confirmPassword: ''
})

// ======================== 表单验证规则 ========================
const nameRules = {
  newName: [
    { required: true, message: '请输入新用户名', trigger: 'blur' },
    { min: 2, message: '用户名长度不能少于2位', trigger: 'blur' }
  ]
}

const passwordRules = {
  oldPassword: [{ required: true, message: '请输入原密码', trigger: 'blur' }],
  newPassword: [
    { required: true, message: '请输入新密码', trigger: 'blur' },
    { min: 6, message: '密码长度不能少于6位', trigger: 'blur' }
  ],
  confirmPassword: [
    { required: true, message: '请确认密码', trigger: 'blur' },
    {
      validator: (rule, value, callback) => {
        if (value !== passwordForm.newPassword) {
          callback(new Error('两次输入密码不一致'))
        } else {
          callback()
        }
      },
      trigger: 'blur'
    }
  ]
}

// ======================== 计算属性 ========================
const addressDialogTitle = computed(() => {
  return dialogType.value === 'add' ? '新增地址' : '编辑地址'
})

// ======================== 引用表单组件 ========================
const nameFormRef = ref(null)
const passwordFormRef = ref(null)

// ======================== 方法定义 ========================

// 获取用户信息
const fetchUserInfo = async () => {
  try {
    const res = await getUserInfo()
    userInfo.name = res.data.name
    userInfo.amount = res.data.amount ? `¥${parseFloat(res.data.amount).toFixed(2)}` : '¥0.00'
    // 如果有头像数据，使用用户头像，否则使用默认头像
    if (res.data.avatar) {
      userInfo.avatar = res.data.avatar
    }
  } catch (err) {
    ElMessage.error('获取用户信息失败')
  }
}

// 处理地址表单提交
const handleAddressFormSubmit = async () => {
  if (addressFormRef.value) {
    await addressFormRef.value.submit()
  }
}

// 获取地址列表
const fetchAddressList = async () => {
  loading.value = true
  try {
    const res = await getAddressList()
    addressList.value = res.data.map(item => ({
      ...item,
      regionName: [item.provinceName, item.cityName, item.districtName].join(' ')
    }))
  } catch (err) {
    ElMessage.error('获取地址失败')
  } finally {
    loading.value = false
  }
}

// 显示修改用户名弹窗
const showChangeNameDialog = () => {
  nameForm.newName = userInfo.name // 将当前用户名填入表单
  nameDialogVisible.value = true
}

// 关闭修改用户名弹窗
const closeNameDialog = () => {
  nameDialogVisible.value = false
  nameForm.newName = ''
  if (nameFormRef.value) {
    nameFormRef.value.clearValidate()
  }
}

// 处理修改用户名
const handleChangeName = async () => {
  try {
    await nameFormRef.value.validate()
    
    await updateUser(nameForm.newName)
    
    userInfo.name = nameForm.newName // 更新本地数据
    ElMessage.success('用户名修改成功')
    closeNameDialog()
  } catch (err) {
    ElMessage.error('用户名修改失败')
  }
}

// 显示修改密码弹窗
const showChangePasswordDialog = () => {
  passwordDialogVisible.value = true
}

// 关闭修改密码弹窗
const closePasswordDialog = () => {
  passwordDialogVisible.value = false
  passwordForm.oldPassword = ''
  passwordForm.newPassword = ''
  passwordForm.confirmPassword = ''
  if (passwordFormRef.value) {
    passwordFormRef.value.clearValidate()
  }
}

// 处理修改密码
const handleChangePassword = async () => {
  try {
    await passwordFormRef.value.validate()
    
    await changePassword({
      oldPassword: passwordForm.oldPassword,
      newPassword: passwordForm.newPassword
    })
    
    ElMessage.success('密码修改成功')
    closePasswordDialog()
  } catch (err) {
    ElMessage.error('密码修改失败')
  }
}

// 显示地址弹窗
const showAddressDialog = (type, address = null) => {
  dialogType.value = type
  currentAddress.value = address ? { ...address } : null
  addressDialogVisible.value = true
}

// 关闭地址弹窗
const closeAddressDialog = () => {
  addressDialogVisible.value = false
  currentAddress.value = null
}

// 删除地址
const deleteAddress = async (id) => {
  try {
    await ElMessageBox.confirm('确定要删除该地址吗？', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    })
    
    await delAddress(id)
    ElMessage.success('删除成功')
    fetchAddressList()
  } catch (err) {
    if (err !== 'cancel') {
      ElMessage.error('删除失败')
    }
  }
}

// 设置默认地址
const setDefaultAddress = async (id) => {
  try {
    await updateAddress({ id, isDefault: 1 })
    await fetchAddressList()
    ElMessage.success('设置成功')
  } catch (err) {
    ElMessage.error('设置失败')
  }
}

// 处理地址提交
const handleAddressSubmit = async (data) => {
  try {
    if (data.id) {
      await updateAddress(data)
    } else {
      await addAddress({ ...data, userId: userInfo.id || 1 })
    }
    ElMessage.success('保存成功')
    closeAddressDialog()
    fetchAddressList()
  } catch (err) {
    ElMessage.error('保存失败')
  }
}

onMounted(async () => {
  await fetchUserInfo()
  await fetchAddressList()
})
</script>

<style scoped>
.personal-center {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
  background: linear-gradient(135deg, #fdf6e3 0%, #faf4eb 100%);
  min-height: 100vh;
}

.page-header {
  margin-bottom: 30px;
  text-align: center;
}

.page-header h2 {
  margin: 0;
  color: #b38b2d;
  font-size: 28px;
  font-weight: 600;
  background: linear-gradient(135deg, #d6bb70, #b38b2d);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.page-subtitle {
  margin: 8px 0 0;
  color: #8a7c5a;
  font-size: 14px;
}

.user-card, .address-card {
  margin-bottom: 24px;
  border-radius: 16px;
  overflow: hidden;
  border: 1px solid #e8dfc8;
  background: rgba(255, 253, 247, 0.9);
  backdrop-filter: blur(10px);
}

:deep(.el-card__header) {
  background: linear-gradient(135deg, #f9f3e5 0%, #f5ecd8 100%);
  border-bottom: 1px solid #e8dfc8;
  padding: 16px 20px;
}


.card-title {
  font-size: 18px;
  font-weight: 600;
  color: #b38b2d;
}

.card-icon {
  color: #d6bb70;
  font-size: 20px;
}

.user-info {
  display: flex;
  align-items: flex-start;
  gap: 40px;
  padding: 10px 0;
}

.user-avatar {
  flex-shrink: 0;
  text-align: center;
}

.user-name {
  margin-top: 12px;
  font-weight: 500;
  color: #8a7c5a;
  font-size: 16px;
}

.user-details {
  flex: 1;
}

.info-item {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  padding: 12px 0;
  border-bottom: 1px solid #f0e9d7;
}

.info-item:last-child {
  border-bottom: none;
}

.info-item .label {
  display: flex;
  align-items: center;
  color: #8a7c5a;
  font-weight: 500;
  width: 120px;
  flex-shrink: 0;
}

.info-item .label .el-icon {
  margin-right: 8px;
  font-size: 16px;
  color: #d6bb70;
}

.info-item .value {
  color: #5d5340;
  font-size: 16px;
}

.info-item .amount {
  color: #d6bb70;
  font-weight: bold;
  font-size: 20px;
  text-shadow: 0 1px 2px rgba(0,0,0,0.1);
}

.action-buttons {
  margin-top: 25px;
  display: flex;
  gap: 12px;
}

.action-btn {
  display: flex;
  align-items: center;
  gap: 6px;
  border-radius: 8px;
  padding: 10px 20px;
  font-weight: 500;
  transition: all 0.3s ease;
  border: 1px solid #d6bb70;
}

.action-btn:deep(.el-icon) {
  font-size: 14px;
}

.action-btn.primary {
  background: linear-gradient(135deg, #d6bb70, #c4a85f);
  border-color: #d6bb70;
  color: white;
}

.action-btn.primary:hover {
  background: linear-gradient(135deg, #c4a85f, #b38b2d);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(214, 187, 112, 0.3);
}

.action-btn.secondary {
  background: transparent;
  color: #d6bb70;
  border-color: #d6bb70;
}

.action-btn.secondary:hover {
  background: #d6bb70;
  color: white;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(214, 187, 112, 0.3);
}

.add-address-btn {
  display: flex;
  align-items: center;
  gap: 6px;
  border-radius: 8px;
  padding: 12px 24px;
  background: linear-gradient(135deg, #d6bb70, #c4a85f);
  border: none;
  color: white;
  font-weight: 500;
  transition: all 0.3s ease;
}

.add-address-btn:hover {
  background: linear-gradient(135deg, #c4a85f, #b38b2d);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(214, 187, 112, 0.3);
}

.address-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 20px;
}

.address-item {
  border: 1px solid #e8dfc8;
  border-radius: 12px;
  padding: 20px;
  transition: all 0.3s ease;
  background: rgba(255, 253, 247, 0.7);
  position: relative;
  backdrop-filter: blur(5px);
}

.address-item:hover {
  box-shadow: 0 6px 20px rgba(214, 187, 112, 0.15);
  transform: translateY(-3px);
  border-color: #d6bb70;
}

.default-address {
  border-color: #d6bb70;
  background: linear-gradient(135deg, #fdf8e8 0%, #faf4eb 100%);
  box-shadow: 0 4px 15px rgba(214, 187, 112, 0.2);
}

.default-address::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background: linear-gradient(135deg, #d6bb70, #b38b2d);
  border-radius: 12px 0 0 12px;
}

.address-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 12px;
  padding-bottom: 12px;
  border-bottom: 1px solid #f0e9d7;
}

.address-consignee {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 600;
  font-size: 16px;
  color: #5d5340;
}

.default-tag {
  background: linear-gradient(135deg, #d6bb70, #c4a85f);
  border: none;
  color: white;
  font-weight: 500;
}

.address-phone {
  display: flex;
  align-items: center;
  gap: 6px;
  color: #8a7c5a;
  font-size: 14px;
}

.address-phone .el-icon {
  color: #d6bb70;
}

.address-content {
  margin-bottom: 16px;
}

.address-region {
  display: flex;
  align-items: center;
  gap: 6px;
  color: #8a7c5a;
  font-size: 14px;
  margin-bottom: 8px;
}

.address-region .el-icon {
  color: #d6bb70;
}

.address-detail {
  color: #5d5340;
  line-height: 1.5;
  font-size: 14px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
}

.header-left {
  display: flex;
  align-items: center;
  gap: 8px;
}

.header-right {
  display: flex;
  align-items: center;
}

/* 移除原来单独的 .address-actions 样式，因为按钮现在在header里 */
.address-actions {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

/* 其他样式保持不变 */

.address-action-btn {
  display: flex;
  align-items: center;
  gap: 4px;
  border-radius: 6px;
  padding: 6px 12px;
  font-size: 12px;
  transition: all 0.3s ease;
  border: 1px solid #e8dfc8;
  background: transparent;
  color: #8a7c5a;
}

.address-action-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.address-action-btn:deep(.el-icon) {
  font-size: 12px;
}

.address-action-btn.danger {
  color: #f56c6c;
  border-color: #fbc4c4;
}

.address-action-btn.danger:hover {
  background: #f56c6c;
  color: white;
}

.address-action-btn.secondary {
  color: #d6bb70;
  border-color: #d6bb70;
}

.address-action-btn.secondary:hover {
  background: #d6bb70;
  color: white;
}

.empty-address {
  grid-column: 1 / -1;
  text-align: center;
  padding: 40px 0;
}

:deep(.el-empty__description) {
  color: #8a7c5a;
}

.dialog-footer {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .personal-center {
    padding: 15px;
  }
  
  .user-info {
    flex-direction: column;
    gap: 20px;
    text-align: center;
  }
  
  .info-item {
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 8px;
  }
  
  .info-item .label {
    width: auto;
    justify-content: center;
  }
  
  .action-buttons {
    justify-content: center;
  }
  
  .address-list {
    grid-template-columns: 1fr;
  }
}
</style>