<!-- views/SearchView.vue -->
<template>
  <div class="search-page">
    <!-- 搜索标题 -->
    <div class="search-header">
      <h1>搜索结果</h1>
      <p v-if="keyword" class="search-tip">
        找到与 "<strong>{{ keyword }}</strong>" 相关的 {{ results.length }} 个商品
      </p>
    </div>

    <!-- 结果容器 -->
    <div class="cake-list" v-if="results.length > 0">
      <el-row :gutter="20">
        <el-col
          v-for="cake in results"
          :key="cake.id"
          :xs="24" :sm="12" :md="8" :lg="6"
        >
          <CakeCard :cake="cake" class="cake-card" @click="goToDetail(cake.id)">
            <template #actions>
              <el-button size="small" @click.stop="handleEdit(cake)" class="edit-btn">编辑</el-button>
              <el-button size="small" @click.stop="handleDelete(cake.id)" class="delete-btn">删除</el-button>
            </template>
          </CakeCard>
        </el-col>
      </el-row>
      
    </div>

    <!-- 无结果提示 -->
    <div v-else class="no-results" v-show="loaded">
      <el-empty
        description="抱歉，没有找到相关商品"
        :image-size="100"
      >
        <el-button type="primary" @click="$router.push('/')" class="home-btn">去首页看看</el-button>
      </el-empty>
    </div>

    <!-- 加载状态 -->
    <div v-if="loading" class="loading">
      <el-skeleton
        v-for="n in 6"
        :key="n"
        :rows="3"
        animated
      />
    </div>

    <!-- 编辑表单对话框 -->
    <el-dialog
      v-if="selectedCake"
      :title="`编辑 ${getCategoryName(selectedCake.categoryId)} 商品`"
      v-model="showEditDialog"
      width="50%"
      @close="closeDialog"
      class="edit-dialog"
    >
      <component 
        :is="getFormComponent(selectedCake.categoryId)"
        :initial-data="selectedCake"
        ref="form"
        @submit="handleSubmit"
        @cancel="closeDialog"
      ></component>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, onMounted, watch,nextTick } from 'vue'
import { useRoute } from 'vue-router'
import CakeCard from '@/components/CakeCard.vue' // 引入你的蛋糕卡片
import CakeForm from '@/components/CakeForm.vue' // 蛋糕表单
import DessertForm from '@/components/DessertForm.vue' // 甜品表单
import PetCakeForm from '@/components/PetCakeForm.vue' // 宠物蛋糕表单
import { searchProducts,update } from '@/api/search' // 假设你有这个 API
import { ElMessage } from 'element-plus'
import router from '@/router'

const route = useRoute()
const keyword = ref('')
const results = ref([])
const loading = ref(false)
const loaded = ref(false) // 控制是否已加载完成
const selectedCake = ref(null) // 当前选中的蛋糕
const showEditDialog = ref(false) // 控制对话框显示
const currentProduct = ref(null)
const form = ref(null)

// 从路由参数获取关键词
const getKeyword = () => {
  const q = route.query.q
  keyword.value = q ? decodeURIComponent(q) : ''
}

// 执行搜索
const performSearch = async () => {
  if (!keyword.value.trim()) {
    results.value = []
    loaded.value = true
    return
  }

  loading.value = true
  try {
    const res = await searchProducts(keyword.value)
    // 假设后端返回 { data: [...] }
    results.value = res.data || []
  } catch (error) {
    ElMessage.error('搜索失败')
    results.value = []
  } finally {
    loading.value = false
    loaded.value = true
  }
}

// 页面加载时执行
onMounted(() => {
  getKeyword()
  performSearch()
})

// 监听路由变化（比如用户修改了 ?q=xxx）
watch(
  () => route.query.q,
  (newQ) => {
    const decoded = newQ ? decodeURIComponent(newQ) : ''
    keyword.value = decoded
    performSearch()
  }
)

// 动态设置页面标题
watch(
  () => keyword.value,
  (kw) => {
    document.title = kw ? `搜索 "${kw}" - 蛋糕商城` : '搜索 - 蛋糕商城'
  },
  { immediate: true }
)

// 根据分类ID获取对应的表单组件
const getFormComponent = (categoryId) => {
  switch (categoryId) {
    case 1:
      return CakeForm;
    case 2:
      return DessertForm;
    case 3:
      return PetCakeForm;
    default:
      return null;
  }
}

//商品详情
const goToDetail = (id) => {
  router.push(`/cake/${id}`)
}

// 获取分类名称
const getCategoryName = (categoryId) => {
  switch (categoryId) {
    case 1:
      return '甄选蛋糕';
    case 2:
      return '甜品';
    case 3:
      return '宠物蛋糕';
    default:
      return '未知';
  }
}

// 处理编辑按钮点击事件
const handleEdit = async(product) => {

  selectedCake.value = {...product};
  showEditDialog.value = true;

   // 等待对话框和组件渲染完成
  await nextTick(async () => {
   
      if (form.value && typeof form.value.setFormData === 'function') {
        
        form.value.setFormData(product)
      } else {
        console.warn('form 或 setFormData 不存在')
      }
    })
  
}

// 提交表单
const handleSubmit = (data) => {
  // 这里可以处理提交逻辑，比如更新商品信息
  console.log("sp",data)
  try{
    update(data);
    ElMessage.success("更新成功");
  }catch(error){
    ElMessage.error("更新失败")
  }
  closeDialog();
}

// 关闭对话框
const closeDialog = () => {
  showEditDialog.value = false;
  selectedCake.value = null;
}
</script>

<style scoped>
.search-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  background-color: #fefaf0;
}

.search-header {
  margin-bottom: 30px;
  text-align: center;
}

.search-header h1 {
  font-size: 28px;
  color: #d6bb70;
  margin-bottom: 10px;
  font-weight: 600;
}

.search-tip {
  color: #666;
  font-size: 14px;
}

.search-tip strong {
  color: #d6bb70;
}

.cake-list {
  min-height: 400px;
}

.cake-card {
  background-color: #fffdf7;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(214, 187, 112, 0.15);
  transition: all 0.3s ease;
  border: 1px solid #f5ecd8;
}

.cake-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(214, 187, 112, 0.25);
}

/* 编辑按钮样式 */
.edit-btn {
  background: #f5ecd8;
  color: #b38b2d;
  border: none;
  border-radius: 15px;
  transition: all 0.3s;
}

.edit-btn:hover {
  background: #d6bb70;
  color: white;
}

/* 删除按钮样式 */
.delete-btn {
  background: #fdf6f0;
  color: #d6bb70;
  border: 1px solid #d6bb70;
  border-radius: 15px;
  transition: all 0.3s;
  margin-right: 8px;
}

.delete-btn:hover {
  background: #d6bb70;
  color: white;
}

.no-results {
  text-align: center;
  margin: 60px 0;
  color: #999;
}

.home-btn {
  background-color: #d6bb70;
  border: none;
  color: white;
  font-weight: 500;
  transition: all 0.3s;
}

.home-btn:hover {
  background-color: #c4a85f;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(214, 187, 112, 0.3);
}

.loading {
  margin-top: 20px;
}

/* 编辑对话框样式 */
:deep(.edit-dialog .el-dialog) {
  border-radius: 12px;
  background-color: #fffdf7;
  border: 1px solid #f5ecd8;
}

:deep(.edit-dialog .el-dialog__header) {
  background-color: #fefaf0;
  border-bottom: 1px solid #f5ecd8;
  border-radius: 12px 12px 0 0;
}

:deep(.edit-dialog .el-dialog__title) {
  color: #d6bb70;
  font-weight: 600;
}

/* 响应式优化 */
@media (max-width: 768px) {
  .search-page {
    padding: 15px;
  }
  
  .search-header h1 {
    font-size: 24px;
  }
}
</style>