import request from '@/utils/request'


// 获取所有蛋糕商品
export function getCakes(params) {
  return request({
    url: '/cakes/list',
    method: 'get',
    params: params
  })
}

// 新增蛋糕商品
export function addCake(data) {
  return request({
    url: '/cakes/add',
    method: 'post',
    data,
    headers:{
      'Content-Type': 'application/json;charset=utf-8'
    }
  })
}
//根据蛋糕id获得蛋糕详情
export function getCakeById(id){
  return request({
    url:`/cakes/get/${id}`,
    method:'get'
  })
}

//修改蛋糕信息
export function updateCake(data){
  return request({
    url:'/cakes/update',
    method:'put',
    data: data,
    headers: {
      'Content-Type': 'application/json;charset=utf-8'
    }
  })
}

//删除蛋糕
export function deleteCake(id){
  return request({
    url: `/cakes/delete/${id}`,
    method: 'delete'
  })
}

//获取口味列表
export function getTastes(){
  return request({
    url: '/cakes/taste/list',
    method: 'get'
  })
}

//更新尺寸信息
export function saveSpecs(productId,specs){
  return request({
    url: `/update/${productId}`,
    method: 'post',
    data: specs
  })
}