<template>
  <div class="payment-container">
    <!-- 顶部进度条 -->
    <div class="progress-steps">
      <div class="step completed">
        <div class="step-circle">1</div>
        <span class="step-text">我的购物车</span>
      </div>
      <div class="step completed">
        <div class="step-circle">2</div>
        <span class="step-text">填写核对订单信息</span>
      </div>
      <div class="step active">
        <div class="step-circle">3</div>
        <span class="step-text">成功提交订单</span>
      </div>
    </div>

    <!-- 订单信息 -->
    <el-card shadow="never" class="payment-card">
      <div class="order-header">
        <el-icon class="success-icon"><CircleCheck /></el-icon>
        <div class="order-info">
          <h3>订单提交成功！请尽快完成支付</h3>
          <p>订单号：{{ orderInfo.orderNo }}</p>
          <p class="order-amount">应付金额：<span class="amount">¥{{ orderInfo.totalAmount }}</span></p>
        </div>
      </div>

      <!-- 支付倒计时 -->
      <div class="countdown-section">
        <div class="countdown-title">支付剩余时间</div>
        <div class="countdown-timer">
          <span class="time-unit">{{ countdown.hours }}</span>
          <span class="time-separator"> ： </span>
          <span class="time-unit">{{ countdown.minutes }}</span>
          <span class="time-separator"> ： </span>
          <span class="time-unit">{{ countdown.seconds }}</span>
        </div>
        <p class="countdown-tip">请在倒计时结束前完成支付，超时订单将自动取消</p>
      </div>

      <!-- 支付方式 -->
      <div class="payment-methods">
        <h4>选择支付方式</h4>
        <div class="payment-options">
          <div
            class="payment-method"
            :class="{ active: selectedPayment === 1 }"
            @click="selectedPayment = 1"
          >
            <div class="payment-icon wechat-icon">微</div>
            <span>微信支付</span>
          </div>
          <div
            class="payment-method"
            :class="{ active: selectedPayment === 2 }"
            @click="selectedPayment = 2"
          >
            <div class="payment-icon alipay-icon">支</div>
            <span>支付宝</span>
          </div>
        </div>
      </div>

      <!-- 密码输入区域 -->
      <div class="password-section" v-if="showPasswordInput">
        <h4>请输入支付密码</h4>
        <div class="password-input-container">
          <el-input
            v-model="password"
            :type="showPassword ? 'text' : 'password'"
            placeholder="请输入支付密码"
            :maxlength="20"
            @input="onPasswordInput"
            class="password-input"
            :disabled="paying"
            @keyup.enter="confirmPayment"
          >
            <template #suffix>
              <el-icon @click="togglePasswordVisibility" style="cursor: pointer;">
                <View v-if="!showPassword" />
                <Hide v-else />
              </el-icon>
            </template>
          </el-input>
          <div class="password-mask" v-if="!showPassword">
            <span
              v-for="i in password.length"
              :key="i"
              class="password-asterisk"
            >*</span>
          </div>
        </div>
        <p class="password-tip">请输入6-20位支付密码</p>
      </div>

      <!-- 操作按钮 -->
      <div class="payment-actions">
        <el-button @click="showCancelDialog" class="cancel-btn" :loading="canceling">
          取消订单
        </el-button>
        <el-button 
          type="primary" 
          @click="handlePayment" 
          class="pay-btn"
          :loading="paying"
          :disabled="!canPay"
        >
          {{ showPasswordInput ? '确认支付' : '立即支付' }} ¥{{ orderInfo.totalAmount }}
        </el-button>
      </div>

      <!-- 订单提示 -->
      <div class="order-tips">
        <p>当日配送订单，不可修改。非当日配送订单，需提前一天的20:00之前，可对部分订单内容进行修改</p>
      
      </div>
    </el-card>

    <!-- 取消订单弹窗 -->
    <el-dialog
      v-model="cancelDialogVisible"
      title="取消订单"
      width="500px"
      :before-close="handleCancelDialogClose"
    >
      <div class="cancel-dialog-content">
        <p class="cancel-tip">确定要取消这个订单吗？请选择取消原因：</p>
        
        <el-form :model="cancelForm" :rules="cancelRules" ref="cancelFormRef">
          <el-form-item prop="cancelReason">
            <el-select
              v-model="cancelForm.cancelReason"
              placeholder="请选择取消原因"
              class="cancel-reason-select"
              size="large"
            >
              <el-option
                v-for="reason in cancelReasons"
                :key="reason.value"
                :label="reason.label"
                :value="reason.value"
              />
            </el-select>
          </el-form-item>
          
          <el-form-item v-if="cancelForm.cancelReason === 'other'" prop="otherReason">
            <el-input
              v-model="cancelForm.otherReason"
              type="textarea"
              placeholder="请输入其他原因"
              :rows="3"
              maxlength="200"
              show-word-limit
            />
          </el-form-item>
        </el-form>
      </div>
      
      <template #footer>
        <div class="dialog-footer">
          <el-button @click="handleCancelDialogClose" :disabled="canceling">
            再想想
          </el-button>
          <el-button 
            type="primary" 
            @click="confirmCancelOrder" 
            :loading="canceling"
            :disabled="!cancelForm.cancelReason"
          >
            确定取消
          </el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>



<script setup>
import { ref, reactive, onMounted, onUnmounted, computed } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { ElMessage, ElMessageBox } from 'element-plus'
import { CircleCheck, View, Hide } from '@element-plus/icons-vue'
import { cancelOrder as cancelOrderApi, getOrderDetail, payOrder } from '@/api/order'

const route = useRoute()
const router = useRouter()

// 订单信息
const orderInfo = ref({
  id: '',
  orderNo: '',
  totalAmount: '0.00',
})

// 支付方式
const selectedPayment = ref(1)

// 密码相关
const password = ref('')
const showPasswordInput = ref(false)
const showPassword = ref(false)

// 倒计时
const countdown = reactive({
  hours: '00',
  minutes: '00',
  seconds: '00'
})

// 加载状态
const paying = ref(false)
const canceling = ref(false)

// 取消订单相关
const cancelDialogVisible = ref(false)
const cancelFormRef = ref()
const cancelForm = reactive({
  cancelReason: '',
  otherReason: ''
})

// 取消原因选项
const cancelReasons = [
  { value: 'change_mind', label: '不想买了' },
  { value: 'wrong_info', label: '信息填写错误' },
  { value: 'found_cheaper', label: '发现更便宜的商品' },
  { value: 'out_of_stock', label: '商品缺货' },
  { value: 'delivery_issue', label: '配送时间不合适' },
  { value: 'payment_issue', label: '支付遇到问题' },
  { value: 'timeout', label: '支付超时' },
  { value: 'other', label: '其他原因' }
]

// 表单验证规则
const cancelRules = {
  cancelReason: [
    { required: true, message: '请选择取消原因', trigger: 'change' }
  ],
  otherReason: [
    { required: true, message: '请输入其他原因', trigger: 'blur' }
  ]
}

// 倒计时定时器
let countdownTimer = null
const totalSeconds = ref(30 * 60) // 30分钟倒计时

// 计算属性：是否可以支付
const canPay = computed(() => {
  return orderInfo.value.id && totalSeconds.value > 0
})

// 监听密码输入
const onPasswordInput = (value) => {
  password.value = value
}

// 切换密码可见性
const togglePasswordVisibility = () => {
  showPassword.value = !showPassword.value
}

// 处理支付按钮点击
const handlePayment = () => {
  if (!showPasswordInput.value) {
    // 第一次点击，显示密码输入框
    showPasswordInput.value = true
    // 聚焦到密码输入框
    setTimeout(() => {
      const passwordInput = document.querySelector('.password-input input')
      if (passwordInput) {
        passwordInput.focus()
      }
    }, 100)
  } else {
    // 已经显示密码输入框，确认支付
    confirmPayment()
  }
}

// 显示取消订单弹窗
const showCancelDialog = () => {
  // 重置表单
  cancelForm.cancelReason = ''
  cancelForm.otherReason = ''
  cancelDialogVisible.value = true
}

// 处理取消弹窗关闭
const handleCancelDialogClose = () => {
  if (!canceling.value) {
    cancelDialogVisible.value = false
  }
}

// 确认取消订单
const confirmCancelOrder = async () => {
  // 验证表单
  if (!cancelForm.cancelReason) {
    ElMessage.error('请选择取消原因')
    return
  }

  if (cancelForm.cancelReason === 'other' && !cancelForm.otherReason) {
    ElMessage.error('请输入其他原因')
    return
  }

  canceling.value = true
  try {
    // 准备取消数据 - 统一使用 cancelReason 字段
    const cancelData = {
      id: orderInfo.value.id,
      cancelReason: cancelForm.cancelReason === 'other' ? 
                   cancelForm.otherReason : 
                   cancelReasons.find(r => r.value === cancelForm.cancelReason)?.label
    }

    console.log('发送取消订单请求:', cancelData)
    
    // 调用取消订单API
    await cancelOrderApi(cancelData)
    
    ElMessage.success('订单已取消')
    cancelDialogVisible.value = false
    router.push('/order')
  } catch (error) {
    console.error('取消订单失败:', error)
    ElMessage.error('取消订单失败，请重试')
  } finally {
    canceling.value = false
  }
}

// 初始化倒计时
const initCountdown = () => {
  countdownTimer = setInterval(() => {
    if (totalSeconds.value <= 0) {
      clearInterval(countdownTimer)
      handleTimeout()
      return
    }

    totalSeconds.value--
    updateCountdownDisplay()
  }, 1000)
}

// 更新倒计时显示
const updateCountdownDisplay = () => {
  const hours = Math.floor(totalSeconds.value / 3600)
  const minutes = Math.floor((totalSeconds.value % 3600) / 60)
  const seconds = totalSeconds.value % 60

  countdown.hours = hours.toString().padStart(2, '0')
  countdown.minutes = minutes.toString().padStart(2, '0')
  countdown.seconds = seconds.toString().padStart(2, '0')
}

// 超时处理
const handleTimeout = async () => {
  try {
    if (orderInfo.value.id) {
      // 超时取消也使用相同的 cancelReason 字段
      const cancelData = {
        id: orderInfo.value.id,
        cancelReason: '支付超时自动取消'
      }
      await cancelOrderApi(cancelData)
    }
    ElMessage.warning('订单支付超时，已自动取消')
    router.push('/order')
  } catch (error) {
    console.error('取消订单失败:', error)
    router.push('/order')
  }
}

// 确认支付 - 调用API验证密码
const confirmPayment = async () => {
  if (!orderInfo.value.id) {
    ElMessage.error('订单信息异常，无法支付')
    return
  }

  if (totalSeconds.value <= 0) {
    ElMessage.error('订单已超时，无法支付')
    return
  }

  if (!password.value) {
    ElMessage.error('请输入支付密码')
    return
  }

  if (password.value.length < 6) {
    ElMessage.error('支付密码至少6位')
    return
  }

  paying.value = true
  try {
    // 调用支付API，传递密码进行验证
    const paymentData = {
      id: orderInfo.value.id,
      payMethod: selectedPayment.value,
      totalAmount: orderInfo.value.totalAmount,
      password: password.value
    }

    console.log('发送支付请求:', paymentData)
    
    // 调用后端API验证密码并完成支付
    const res = await payOrder(paymentData)
    console.log("支付信息：", res)
    
    if (res.code === 200) {
      ElMessage.success('支付成功！')
      // 支付成功，跳转到订单页面
      router.push('/order')
    } else {
      // 密码错误或其他错误
      ElMessage.error(res.message || '支付失败，请检查密码是否正确')
      // 清空密码，让用户重新输入
      password.value = ''
      // 重新聚焦到密码输入框
      setTimeout(() => {
        const passwordInput = document.querySelector('.password-input input')
        if (passwordInput) {
          passwordInput.focus()
        }
      }, 100)
    }
  } catch (error) {
    console.error('支付请求失败:', error)
    ElMessage.error('支付失败，请重试')
    // 清空密码，让用户重新输入
    password.value = ''
  } finally {
    paying.value = false
  }
}

// 查看订单详情
const viewOrderDetail = () => {
  if (orderInfo.value.id) {
    router.push(`/order/detail/${orderInfo.value.id}`)
  } else {
    router.push('/order')
  }
}

// 继续购物
const continueShopping = () => {
  router.push('/')
}

// 加载订单数据
const loadOrderData = async () => {
  // 直接从路由查询参数中获取
  const { orderId, orderNo, amount } = route.query
  
  if (!orderId && !orderNo) {
    ElMessage.error('订单信息不存在')
    router.back()
    return
  }
  
  // 如果API调用失败，使用路由参数
  orderInfo.value = {
    id: orderId || '',
    orderNo: orderNo || `ORDER${Date.now()}`,
    totalAmount: amount || '0.00',
  }
}

onMounted(() => {
  loadOrderData()
  updateCountdownDisplay()
  initCountdown()
})

onUnmounted(() => {
  if (countdownTimer) {
    clearInterval(countdownTimer)
  }
})
</script>

<style scoped>
/* 样式保持不变 */
</style>

<style scoped>
.payment-container {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  background-color: #fefaf0;
}

/* 进度条样式 */
.progress-steps {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
  position: relative;
}

.progress-steps::before {
  content: '';
  position: absolute;
  top: 20px;
  left: 10%;
  right: 10%;
  height: 2px;
  background-color: #e8e8e8;
  z-index: 1;
}

.step {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  z-index: 2;
  flex: 1;
}

.step-circle {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #f5f5f5;
  border: 2px solid #e8e8e8;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  margin-bottom: 8px;
}

.step.completed .step-circle {
  background-color: #d6bb70;
  border-color: #d6bb70;
  color: white;
}

.step.active .step-circle {
  background-color: #d6bb70;
  border-color: #d6bb70;
  color: white;
}

.step-text {
  font-size: 14px;
  color: #999;
}

.step.completed .step-text,
.step.active .step-text {
  color: #d6bb70;
  font-weight: 500;
}

/* 支付卡片样式 */
.payment-card {
  background-color: #fffdf7;
  border: 1px solid #f5ecd8;
  border-radius: 12px;
  padding: 30px;
}

.order-header {
  display: flex;
  align-items: flex-start;
  gap: 16px;
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 1px solid #f5ecd8;
}

.success-icon {
  color: #67c23a;
  font-size: 40px;
  margin-top: 4px;
}

.order-info h3 {
  color: #333;
  margin: 0 0 8px 0;
  font-size: 18px;
}

.order-info p {
  margin: 4px 0;
  color: #666;
}

.amount {
  color: #d6bb70;
  font-weight: bold;
  font-size: 20px;
}

/* 倒计时样式 */
.countdown-section {
  text-align: center;
  margin: 30px 0;
  padding: 20px;
  background-color: #fefaf0;
  border-radius: 8px;
  border: 1px solid #f5ecd8;
}

.countdown-title {
  font-size: 16px;
  color: #666;
  margin-bottom: 16px;
}

.countdown-timer {
  font-size: 32px;
  font-weight: bold;
  color: #e6a23c;
  margin-bottom: 8px;
  font-family: 'Courier New', monospace;
}

.time-unit {
  display: inline-block;
  min-width: 40px;
  text-align: center;
}

.time-separator {
  color: #d6bb70;
}

.countdown-tip {
  color: #999;
  font-size: 14px;
  margin: 0;
}

/* 支付方式 */
.payment-methods {
  margin: 30px 0;
}

.payment-methods h4 {
  color: #333;
  margin-bottom: 16px;
  font-size: 16px;
}

.payment-options {
  display: flex;
  gap: 16px;
}

.payment-method {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 16px 20px;
  border: 2px solid #f5ecd8;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s;
  background-color: white;
}

.payment-method:hover {
  border-color: #d6bb70;
}

.payment-method.active {
  border-color: #d6bb70;
  background-color: #fefaf0;
  box-shadow: 0 2px 8px rgba(214, 187, 112, 0.2);
}

.payment-icon {
  width: 32px;
  height: 32px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: bold;
  font-size: 14px;
}

.wechat-icon {
  background-color: #07C160;
}

.alipay-icon {
  background-color: #1677FF;
}

.payment-method span {
  font-size: 16px;
  color: #333;
  font-weight: 500;
}

/* 密码输入区域 */
.password-section {
  margin: 30px 0;
  padding: 20px;
  background-color: #fefaf0;
  border-radius: 8px;
  border: 1px solid #f5ecd8;
}

.password-section h4 {
  color: #333;
  margin-bottom: 16px;
  text-align: center;
  font-size: 16px;
}

.password-input-container {
  position: relative;
  max-width: 300px;
  margin: 0 auto 12px;
}

.password-input :deep(.el-input__inner) {
  height: 50px;
  font-size: 16px;
  text-align: center;
  border: 2px solid #d6bb70;
  border-radius: 8px;
  background-color: white;
  color: transparent; /* 隐藏实际输入的文字 */
}

.password-input :deep(.el-input__inner:focus) {
  border-color: #c4a85f;
  box-shadow: 0 0 0 2px rgba(214, 187, 112, 0.2);
}

.password-mask {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 25px;
  pointer-events: none;
  gap: 8px;
}

.password-asterisk {
  font-size: 24px;
  color: #d6bb70;
  font-weight: bold;
}

.password-tip {
  text-align: center;
  color: #999;
  font-size: 14px;
  margin: 0;
}

/* 操作按钮 */
.payment-actions {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin: 40px 0 20px;
}

.cancel-btn {
  background-color: #f5ecd8;
  color: #d6bb70;
  border: 1px solid #d6bb70;
  border-radius: 8px;
  padding: 12px 32px;
  font-size: 16px;
}

.cancel-btn:hover {
  background-color: #e8d9b0;
}

.pay-btn {
  background-color: #d6bb70;
  border: none;
  border-radius: 8px;
  padding: 12px 32px;
  font-size: 16px;
  font-weight: 500;
}

.pay-btn:hover:not(:disabled) {
  background-color: #c4a85f;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(214, 187, 112, 0.3);
}

.pay-btn:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

/* 订单提示 */
.order-tips {
  text-align: center;
  padding-top: 20px;
  border-top: 1px solid #f5ecd8;
}

.order-tips p {
  color: #999;
  font-size: 14px;
  margin-bottom: 16px;
}

.quick-links {
  display: flex;
  justify-content: center;
  gap: 30px;
}

.view-order-link,
.continue-shopping-link {
  color: #d6bb70;
  font-size: 14px;
}

.view-order-link:hover,
.continue-shopping-link:hover {
  color: #c4a85f;
}

/* 取消订单弹窗样式 */
.cancel-dialog-content {
  padding: 10px 0;
}

.cancel-tip {
  color: #666;
  margin-bottom: 20px;
  font-size: 14px;
}

.cancel-reason-select {
  width: 100%;
}

.dialog-footer {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .payment-container {
    margin: 10px auto;
    padding: 10px;
  }

  .progress-steps {
    flex-direction: column;
    gap: 20px;
  }

  .progress-steps::before {
    display: none;
  }

  .payment-options {
    flex-direction: column;
  }

  .payment-actions {
    flex-direction: column;
  }

  .quick-links {
    flex-direction: column;
    gap: 12px;
  }

  .countdown-timer {
    font-size: 24px;
  }
}
</style>