<template>
  <div class="cart">
    <el-card shadow="never" :body-style="{ padding: '30px' }">
      <h2>我的购物车</h2>

      <!-- 购物车商品列表 -->
      <el-table 
        :data="cartItems" 
        style="width: 100%" 
        v-if="cartItems.length > 0" 
        :show-header="false"
        :row-style="{ height: '100px' }"
      >
        <!-- 选择框 -->
        <el-table-column width="60" align="center">
          <template #default="scope">
            <el-checkbox v-model="scope.row.checked" @change="updateTotal"></el-checkbox>
          </template>
        </el-table-column>

        <!-- 商品信息（主区域，占最大空间） -->
        <el-table-column prop="name" label="商品信息">
          <template #default="scope">
            <div class="product-info" @click="goToProductDetail(scope.row.productId)">
              <img :src="scope.row.image" class="cart-image" />
              <div class="product-details">
                <div class="product-name">{{ scope.row.name }}</div>
                <div class="product-number">编号：{{ scope.row.number }}</div>
              </div>
            </div>
          </template>
        </el-table-column>

        <!-- 中间：价格对比 -->
        <el-table-column label="价格变化" width="200" align="center">
          <template #default="scope">
            <div class="price-comparison">
              <template v-if="scope.row.nowAmount != null && scope.row.amount != null">
                <div v-if="scope.row.nowAmount < scope.row.amount" class="price-down">
                  ↓ 立省 ¥{{ (scope.row.amount - scope.row.nowAmount).toFixed(2) }}
                </div>
                <div v-else-if="scope.row.nowAmount > scope.row.amount" class="price-up">
                  ↑ 涨价 ¥{{ (scope.row.nowAmount - scope.row.amount).toFixed(2) }}
                </div>
                <div v-else>
                  价格未变
                </div>
                <div class="original-price">
                  原价 ¥{{ scope.row.amount }} → 现价 ¥{{ scope.row.nowAmount }}
                </div>
              </template>
              <template v-else>
                <!-- 如果没有nowAmount或amount数据 -->
                价格信息加载中...
              </template>
            </div>
          </template>
        </el-table-column>
        <!-- 单价 -->
        <el-table-column label="单价" width="140" align="center">
          <template #default="scope">
            <span class="price-highlight">¥{{ scope.row.amount }}</span>
          </template>
        </el-table-column>

        <!-- 数量 -->
        <el-table-column label="数量" width="160" align="center">
          <template #default="scope">
            <el-input-number
              size="small"
              v-model="scope.row.num"
              :min="1"
              @change="updateItem(scope.row)"
            ></el-input-number>
          </template>
        </el-table-column>

        <!-- 操作 -->
        <el-table-column label="操作" width="120" align="center">
          <template #default="scope">
            <el-button type="text" @click="removeItem(scope.row.id)" class="delete-btn">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 购物车为空提示 -->
      <div v-else class="empty-cart">
        <el-empty description="您的购物车为空" :image-size="80">
          <template #default>
            <el-button 
              type="primary" 
              class="go-home-btn"
              @click="goHome"
            >
              去首页逛逛
            </el-button>
          </template>
        </el-empty>
      </div>


      <!-- 底部操作栏 -->
      <div class="cart-footer" v-if="cartItems.length > 0">
        <div class="footer-left">
          <el-checkbox v-model="allChecked" @change="toggleAll">全选</el-checkbox>
           <span class="batch-delete-btn" style="margin-left: 24px;" @click="batchDelete">批量删除</span>
           <span class="clear-all-btn" @click="clearAllCart">清空购物车</span>
        </div>
        <div class="footer-right">
          <div class="total-price">
            已选择 <span>{{ checkedCount }}</span> 件商品
          </div>
          <div class="total-price">
            总价：<span class="price-highlight">¥{{ totalPrice }}</span>
          </div>
          <el-button type="primary" size="large" @click="checkout" class="checkout-btn">去结算</el-button>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, watch } from 'vue'
import { ElMessage,ElMessageBox} from 'element-plus'
import { useCartStore } from '@/store/cart'
import { useRouter } from 'vue-router'
import {updateCart,removeCart,batchRemoveCart,clearCart} from '@/api/shoppingCart'

const router = useRouter()
const cartStore = useCartStore()
const cartItems = computed(() => cartStore.items)

// 计算总价
const totalPrice = computed(() => {
  return cartItems.value
    .filter(item => item.checked)
    .reduce((sum, item) => sum + item.amount * item.num, 0)
    .toFixed(2)
})

// 计算已选择的商品数量
const checkedCount = computed(() => {
  return cartItems.value.filter(item => item.checked).length
})

const goHome = () => {
  router.push('/home')
}

// 全选状态
const allChecked = computed({
  get: () => cartItems.value.length > 0 && cartItems.value.every(item => item.checked),
  set: (value) => {
    cartItems.value.forEach(item => {
      item.checked = value
    })
    updateTotal()
  }
})

// 页面加载时从后端加载购物车数据
onMounted(async () => {
  try {
    await cartStore.loadCart()
    // 初始化所有商品为选中状态
    cartItems.value.forEach(item => {
      if (item.checked === undefined) {
        item.checked = true
      }
    })
  } catch (error) {
    ElMessage.error('加载购物车失败，请检查网络')
  }
})

// 更新商品数量
const updateItem = async (item) => {
  try {
    await updateCart(item)
    
  } catch (error) {
    ElMessage.error('更新失败')
  }
}

// 删除商品
const removeItem = async (id) => {
  try {
    await removeCart(id)
    ElMessage.success('删除成功')
    cartStore.loadCart()
  } catch (error) {
    ElMessage.error('删除失败')
  }
}

// 批量删除选中的商品
const batchDelete = async () => {
  const checkedItems = cartItems.value.filter(item => item.checked)
  if (checkedItems.length === 0) {
    ElMessage.warning('请先选择要删除的商品')
    return
  }

  const ids = checkedItems.map(item => item.id) // 提取出所有选中项的 id

  ElMessageBox.confirm(
    `确定要删除选中的 ${ids.length} 件商品吗？`,
    '批量删除',
    {
      type: 'warning',
      confirmButtonText: '删除',
      cancelButtonText: '取消'
    }
  )
    .then(async () => {
      try {
        await batchRemoveCart(ids) // 一次性删除所有选中项
        await cartStore.loadCart()   // 重新加载购物车
        ElMessage.success('批量删除成功')
        updateTotal()
      } catch (error) {
        ElMessage.error('批量删除失败，请重试')
      }
    })
    .catch(() => {
      ElMessage.info('已取消删除')
    })
}

// 清空购物车
const clearAllCart = async () => {

   // 先检查购物车是否为空
  if (cartItems.value.length === 0) {
    ElMessage.info('购物车已经是空的了')
    return
  }
  try {

    // 弹出确认框
    await ElMessageBox.confirm(
      '确定要清空整个购物车吗？',
      '清空购物车',
      {
        confirmButtonText: '清空',
        cancelButtonText: '取消',
        type: 'warning'
      }
    )
    
    await clearCart()
    ElMessage.success('购物车已清空')
    cartStore.loadCart();
  } catch (error) {
    ElMessage.error('清空失败')
  }
}

// 结算
const checkout = () => {
  if (checkedCount.value === 0) {
    ElMessage.warning('请至少选择一件商品')
    return
  }

  //过滤出被选出的商品项
  const selectedItems = cartItems.value.filter(item => item.checked)

  //跳转订单页面
   router.push({
    path: '/checkout',
    query: {items: JSON.stringify(selectedItems)}
  })

}

// 更新总价格和全选状态
const updateTotal = () => {
  // 这里可以添加一些额外的逻辑，比如更新store中的状态
}

// 全选/取消全选
const toggleAll = (value) => {
  cartItems.value.forEach(item => {
    item.checked = value
  })
  updateTotal()
}

// 跳转到商品详情页
const goToProductDetail = (productId) => {
  router.push(`/cake/${productId}`)
}
</script>

<style scoped>
.cart {
  max-width: 1200px;
  margin: 20px auto;
  padding: 0 20px;
  box-sizing: border-box;
}

/* 标题样式 */
h2 {
  color: #d6bb70;
  margin-bottom: 24px;
  text-align: center;
  font-weight: 600;
}

/* 图片样式 */
.cart-image {
  width: 80px;
  height: 80px;
  object-fit: cover;
  border-radius: 8px;
  margin-right: 16px;
  cursor: pointer;
  transition: transform 0.2s;
  border: 1px solid #f5ecd8;
}

.cart-image:hover {
  transform: scale(1.05);
}

/* 商品信息区域 */
.product-info {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 10px 0;
}

.product-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.product-name {
  font-weight: 600;
  font-size: 15px;
  color: #333;
  margin-bottom: 4px;
}

.product-number {
  font-size: 13px;
  color: #999;
}

/* 价格对比样式 */
.price-comparison {
  font-size: 13px;
}

.price-down {
  color: #67c23a;
  font-weight: 500;
  margin-bottom: 4px;
}

.price-up {
  color: #f56c6c;
  font-weight: 500;
  margin-bottom: 4px;
}

.original-price {
  color: #999;
  font-size: 12px;
}

/* 空购物车 */
.empty-cart {
  text-align: center;
  color: #999;
  padding: 60px 0;
}

/* 底部操作栏 */
.cart-footer {
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #f5ecd8;
  padding-top: 24px;
  background-color: #fefaf0;
  border-radius: 8px;
  padding: 20px;
}

.footer-left {
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #666;
}

.batch-delete-btn {
  margin-left: 24px;
  color: #d6bb70;
  cursor: pointer;
  font-weight: 500;
  transition: color 0.3s;
}

.batch-delete-btn:hover {
  color: #c4a85f;
  text-decoration: underline;
}

.clear-all-btn {
  margin-left: 24px;
  color: #e6a23c;
  cursor: pointer;
  font-weight: 500;
  transition: color 0.3s;
}

.clear-all-btn:hover {
  color: #cf9236;
  text-decoration: underline;
}

.footer-right {
  display: flex;
  align-items: center;
  gap: 24px;
}

.total-price {
  font-size: 15px;
  color: #333;
}

.total-price span {
  color: #d6bb70;
  font-weight: bold;
  font-size: 16px;
}

.price-highlight {
  color: #d6bb70;
  font-weight: bold;
  font-size: 16px;
}

/* 删除按钮样式 */
.delete-btn {
  color: #d6bb70;
  transition: color 0.3s;
}

.delete-btn:hover {
  color: #c4a85f;
}

.go-home-btn {
  background-color: #d6bb70;
  border: none;
  color: white;
  font-weight: 500;
  padding: 10px 22px;
  border-radius: 8px;
  transition: all 0.3s;
}

.go-home-btn:hover {
  background-color: #c4a85f;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(214, 187, 112, 0.3);
}


/* 结算按钮样式 */
.checkout-btn {
  background-color: #d6bb70;
  border: none;
  color: white;
  font-weight: 500;
  transition: all 0.3s;
  padding: 12px 24px;
}

.checkout-btn:hover {
  background-color: #c4a85f;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(214, 187, 112, 0.3);
}

/* 复选框样式 */
:deep(.el-checkbox__input.is-checked .el-checkbox__inner) {
  background-color: #d6bb70;
  border-color: #d6bb70;
}

:deep(.el-checkbox__input.is-checked + .el-checkbox__label) {
  color: #d6bb70;
}

/* 数字输入框样式 */
:deep(.el-input-number__increase),
:deep(.el-input-number__decrease) {
  background-color: #f5ecd8;
  color: #d6bb70;
  border-color: #f5ecd8;
}

:deep(.el-input-number__increase:hover),
:deep(.el-input-number__decrease:hover) {
  background-color: #d6bb70;
  color: white;
}

:deep(.el-input-number .el-input__inner) {
  border-color: #f5ecd8;
}

:deep(.el-input-number .el-input__inner:focus) {
  border-color: #d6bb70;
}

/* 表格样式 */
:deep(.el-table) {
  background-color: #fffdf7;
}

:deep(.el-table th) {
  display: none;
}

:deep(.el-table .cell) {
  display: flex;
  align-items: center;
  justify-content: center;
  word-break: normal;
}

:deep(.el-table--enable-row-hover .el-table__body tr:hover>td) {
  background-color: #fefaf0 !important;
}

/* 卡片样式 */
:deep(.el-card) {
  background-color: #fffdf7;
  border: 1px solid #f5ecd8;
  border-radius: 12px;
}

/* 响应式优化 */
@media (max-width: 768px) {
  .cart {
    padding: 0 10px;
  }

  .cart-footer {
    flex-direction: column;
    gap: 12px;
    text-align: center;
  }

  .footer-right {
    justify-content: center;
    gap: 16px;
    flex-wrap: wrap;
  }

  .total-price {
    margin-right: 0;
  }
}
</style>