import request from "@/utils/request";

//添加地址
export function addAddress(data){
    return request({
        url: '/address/add',
        method: 'put',
        data: data
    })
}

export function getAddressList(){
    return request({
        url: "/address/list",
        method: "get"
    })
}

export function updateAddress(data){
    return request({
        url: "/address/update",
        method: "post",
        data: data
    })
}

//删除地址
export function delAddress(id){
    return request({
        url:`/address/delete/${id}`,
        method:"delete"
    })
}