<script setup>
import { ref, onMounted } from 'vue'
import { ElMessage } from 'element-plus'
import { getCategoryRankings } from '@/api/home'
import ProductCard from '@/components/CakeCard.vue'
import { useRouter } from 'vue-router'

const router = useRouter()

// 轮播图数据 - 使用固定数据
const banners = ref([
  {
    title: '金色盛宴 尊享体验',
    subtitle: '每一口都是奢华的享受',
    image: 'https://lsl-bucket.oss-cn-guangzhou.aliyuncs.com/8d45ff2e-48bd-4f25-8c1b-732f9bcdac51.jpg'
  },
  {
    title: '奢华品味 金色时光',
    subtitle: '精选优质原料 匠心制作',
    image: 'https://lsl-bucket.oss-cn-guangzhou.aliyuncs.com/2666eaa3-a09b-4647-9617-d88e5f110159.jpg'
  },
  
  {
    title: '幸福滋味 甜蜜分享',
    subtitle: '让每一刻都充满甜蜜的惊喜',
    image: 'https://lsl-bucket.oss-cn-guangzhou.aliyuncs.com/4e49d436-042d-47fc-8f00-170d323283b7.jpg'
  }
])

// 响应式数据
const recommendations = ref([])
const categoryRankings = ref([])
const loading = ref(false)

// 跳转到商品详情
const goToProductDetail = (productId) => {
  router.push(`/cake/${productId}`)
}

// 获取首页数据
const fetchData = async () => {
  try {
    loading.value = true
    
    // 获取分类排行榜数据
    const rankingRes = await getCategoryRankings()
    
    // 直接赋值给 categoryRankings
    categoryRankings.value = rankingRes.data

    console.log("排行榜数据:",categoryRankings.value)
    
    // 热门推荐可以留空或者从排行榜中取一些数据
    recommendations.value = []
    
  } catch (error) {
    console.error('数据加载失败:', error)
    ElMessage.error('数据加载失败')
  } finally {
    loading.value = false
  }
}

onMounted(fetchData)
</script>

