<template>
  <div class="pet-cakes-page">
    <!-- 固定顶部图片 -->
    <div class="header-image">
      <img src="https://lsl-bucket.oss-cn-guangzhou.aliyuncs.com/2ac94332-a67e-47fa-af2d-cb90fbe985b1.jpg" alt="Header Image" class="header-img">
    </div>


    <!-- 蛋糕列表 -->
    <div class="cake-list">
      <el-row :gutter="20">
        <el-col
          v-for="cake in cakes"
          :key="cake.id"
          :xs="24" :sm="12" :md="8" :lg="6"
        >
          <CakeCard :cake="cake" class="cake-card" @click="goToDetail(cake.id)">
            <template #actions>
              <el-button size="small" @click.stop="handleEdit(cake)" class="edit-btn">编辑</el-button>
              <el-button size="small" @click.stop="handleDelete(cake.id)" class="delete-btn">删除</el-button>
            </template>
          </CakeCard>
        </el-col>
      </el-row>

      <el-empty v-if="loading && cakes.length === 0" description="加载中..." />
      <el-empty v-else-if="!loading && cakes.length === 0" description="暂无宠物蛋糕" />
    </div>

    <!-- 对话框 -->
    <el-dialog 
      :title="isEdit ? '编辑宠物蛋糕' : '新增宠物蛋糕'"
      v-model="addDialogVisible"
      width="50%"
      @closed="handleDialogClosed"
      class="sweet-dialog"
    >
      <pet-cake-form
        ref="petCakeForm"
        :key="isEdit ? 'edit-form' : 'add-form'"
        :is-edit="isEdit"
        :initial-data="currentCakeData"
        @submit="handleAddCake"
        @cancel="addDialogVisible = false"
      />
    </el-dialog>

    
     <div class="dessert-card add-card" @click="showAddDialog">
        <div class="add-card-content">
          <el-icon class="add-icon"><Plus /></el-icon>
          <span class="add-text">添加宠物蛋糕</span>
          <p class="add-tip">打造您的专属宠物蛋糕</p>
        </div>
      </div>


     <!-- 温馨提示弹窗 -->
    <el-dialog
      v-model="showAlert"
      :show-close="false"
      :close-on-click-modal="false"
      width="90%"
      max-width="400px"
      center
      class="sweet-warning-dialog"
    >
      <div class="warning-content">
        <div class="warning-icon">🐾</div>
        <h3 style="color: #d6bb70; margin: 10px 0;">温馨提示</h3>
        <p style="color: #666; line-height: 1.6;">
          这是一个专为宠物设计的蛋糕，关爱宠物，体现主人对宠物的爱。
        </p>
        <p style="font-size: 14px; color: #999; margin-top: 10px;">
          请勿与人类食用蛋糕混淆
        </p>
      </div>
      <template #footer>
        <el-button
          :disabled="!canConfirm"
          @click="closeAlert"
          class="confirm-btn"
        >
          {{ canConfirm ? '我知道了' : `等待 ${countdown}s` }}
        </el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, onMounted,nextTick } from 'vue'
import { ElMessageBox, ElMessage } from 'element-plus'
import { Plus} from '@element-plus/icons-vue'
import CakeCard from '@/components/CakeCard.vue'
import PetCakeForm from '@/components/PetCakeForm.vue'
import { pageQuery,getCakeById, updateCake,saveSpecs,addCake,addPetCake, updatePetCake, deletePetCake } from '@/api/petCake'
import router from '@/router'

// 数据状态
const cakes = ref([])
const loading = ref(false)
const pagination = ref({
  pageNum: 1,     // 当前页
  pageSize: 10,   // 固定每页 10 条
  total: 0        // 总数
})
const isEdit = ref(false) // 判断是否是编辑
const currentPetCakeData = ref(null) //存储当前的宠物蛋糕信息
const currentPetCakeId = ref(null) // 存储当前的宠物蛋糕id
const addDialogVisible = ref(false) //控制对话框的显示
const petCakeForm = ref(null); // 对应 <pet-cake-form ref="petCakeForm">


// 弹窗控制
const showAlert = ref(false)
const canConfirm = ref(false)
const countdown = ref(5)

// 检查是否已经显示过提示
const hasShownWarning = () => {
  return sessionStorage.getItem('petCakeWarningShown') === 'true'
}

// 标记为已显示
const markWarningAsShown = () => {
  sessionStorage.setItem('petCakeWarningShown', 'true')
}

onMounted(async () => {
  await loadFromServer()
  // 只在第一次访问时显示提示
  if (!hasShownWarning()) {
    showWarning()
  }
})

// 显示温馨提示（3秒后可关闭）
const showWarning = () => {
  showAlert.value = true
  canConfirm.value = false
  countdown.value = 5

  const timer = setInterval(() => {
    countdown.value--
    if (countdown.value <= 0) {
      canConfirm.value = true
      clearInterval(timer)
    }
  }, 1000)
}

const closeAlert = () => {
  if (canConfirm.value) {
    showAlert.value = false
    // 关闭时标记为已显示
    markWarningAsShown()
  }
}


// 从后端获取宠物蛋糕数据（已过滤，无需前端再 filter）
const loadFromServer = async () => {
  loading.value = true
  try {
    const params = {
      pageNum: pagination.value.pageNum,
      pageSize: pagination.value.pageSize // 固定为 10
    }
    const res = await pageQuery(params)
    console.log("宠物蛋糕信息：",res)
    cakes.value = res.data.records || []
    pagination.value.total = res.data.total || 0
  } catch (err) {
    console.error('数据加载失败:', err)
    ElMessage.error('数据加载失败')
  } finally {
    loading.value = false
  }
}

//跳转商品详情页
const goToDetail = (id) => {
  router.push(`/cake/${id}`)
}
// 处理页码变化
const handleCurrentChange = (newPage) => {
  pagination.value.pageNum = newPage
  loadFromServer()
}

// 显示温馨提示（3秒后可关闭）
// const showWarning = () => {
//   showAlert.value = true
//   canConfirm.value = false
//   countdown.value = 3

//   const timer = setInterval(() => {
//     countdown.value--
//     if (countdown.value <= 0) {
//       canConfirm.value = true
//       clearInterval(timer)
//     }
//   }, 1000)
// }

// const closeAlert = () => {
//   if (canConfirm.value) {
//     showAlert.value = false
//   }
// }


// 编辑宠物蛋糕
const handleEdit = async(cake) => {
      loading.value = true

      try {
        // 获取蛋糕详情
        isEdit.value = true;
        const res = await getCakeById(cake.id)
        currentPetCakeData.value = { ...res.data };
        console.log("宠物蛋糕信息：",currentPetCakeData.value)
        currentPetCakeId.value = cake.id
        addDialogVisible.value = true
        // 4. 确保表单已渲染后再设置数据
        await nextTick(() => {
          if (petCakeForm.value) {
            petCakeForm.value.setFormData(currentPetCakeData.value);
          }
        });
      } catch (error) {
        ElMessage.error('获取蛋糕详情失败')
        console.error(error)
      } finally {
        loading.value = false
      }
    }
    
const handleAddCake = async(formData) => {
    try {
      

      // 分离商品基本信息和规格信息
      const { specs, ...baseData } = formData;
      
      // 确保包含分类ID
      const categoryId = 3;
      if (categoryId) {
        formData.categoryId = categoryId;
      }
      console.log("分类id:",formData.categoryId)
      
      if (isEdit.value) {
        console.log("更新数据：", { ...baseData, id: currentPetCakeId.value });
        
        // 1. 更新商品基本信息
        await updatePetCake({
          ...baseData,
          id: currentPetCakeId.value
        });
        
        // // 2. 保存规格信息
        await saveSpecs(currentPetCakeId.value, specs);
        
        ElMessage.success('宠物蛋糕更新成功');
      } else {
        
        
        // 1. 新增商品基本信息和添加口味
        await addPetCake(formData);
       ElMessage.success('宠物蛋糕添加成功');
      }
      
      addDialogVisible.value = false;
       // 重新加载数据
      await loadFromServer();
    } catch (error) {
      ElMessage.error(isEdit.value ? '更新失败' : '添加失败');
      console.error(error);
    }
  }

  //显示添加对话框
  const showAddDialog = () =>{
    isEdit.value = false;
    addDialogVisible.value = true;
    currentPetCakeData.value = null;
    currentPetCakeId.value = null

  }

  //删除蛋糕卡片
  const handleDelete = async(id) =>{
    try{
      await ElMessageBox.confirm('确定要删除这个宠物蛋糕吗？','提示',{
        confirmButtonText:'确定',
        cancelButtonText:'取消',
        type:'warning'
      })

      await deletePetCake(id)
      ElMessage.success('删除成功')

       // 如果删除的是当前页的最后一条数据，且不是第一页，则返回上一页
    if (cakes.value.length === 1 && pagination.value.pageNum > 1) {
      pagination.value.pageNum -= 1
    }
      loadFromServer()

    }catch (error) {
      ElMessage.error('删除失败');
      console.error(error);
    }
  }
  



</script>

<style scoped>
.pet-cakes-page {
  padding: 20px 10px;
  max-width: 1200px;
  margin: 0 auto;
  background-color: #fefaf0;
}

.header-image {
  position: relative;
 
  overflow: hidden;
  box-shadow: 0 8px 32px rgba(214, 187, 112, 0.2);
  margin-bottom: 40px;
  height: 400px;
}

.header-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: brightness(90%);
} 

.header-actions {
  text-align: right;
  margin-bottom: 20px;
}

.add-button {
  background-color: #d6bb70;
  color: white;
  font-weight: bold;
  border: none;
  transition: all 0.3s ease;
}

.add-button:hover {
  background-color: #c4a85f;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(214, 187, 112, 0.3);
}

.cake-list {
  min-height: 400px;
}

.cake-card {
  background-color: #fffdf7;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(214, 187, 112, 0.15);
  transition: all 0.3s ease;
  border: 1px solid #f5ecd8;
}

.cake-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(214, 187, 112, 0.25);
}

.add-card {
  background: linear-gradient(135deg, #fffdf5, #f8f4e8);
  border: 2px dashed #d6bb70;
  border-radius: 16px;
  padding: 40px 20px;
  text-align: center;
  cursor: pointer;
  transition: all 0.4s ease;
  margin: 20px auto;
  max-width: 300px;
}

.add-card:hover {
  border-color: #b38b2d;
  background: linear-gradient(135deg, #f8f4e8, #f5f0e1);
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(214, 187, 112, 0.2);
}

.add-card-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
}

.add-icon {
  font-size: 48px;
  color: #d6bb70;
  transition: all 0.3s ease;
}

.add-card:hover .add-icon {
  color: #b38b2d;
  transform: scale(1.1);
}

.add-text {
  font-size: 18px;
  font-weight: 600;
  color: #b38b2d;
}

.add-tip {
  font-size: 14px;
  color: #8a7c5a;
  margin: 0;
}
/* 编辑按钮样式 */
.edit-btn {
  background: #f5ecd8;
  color: #b38b2d;
  border: none;
  border-radius: 15px;
  transition: all 0.3s;
}

.edit-btn:hover {
  background: #d6bb70;
  color: white;
}

/* 删除按钮样式 */
.delete-btn {
  background: #fdf6f0;
  color: #d6bb70;
  border: 1px solid #d6bb70;
  border-radius: 15px;
  transition: all 0.3s;
  margin-right: 8px;
}

.delete-btn:hover {
  background: #d6bb70;
  color: white;
}

.pagination-container {
  margin-top: 30px;
  text-align: center;
}

.pagination-wrapper {
  display: inline-flex;
  align-items: center;
  background: #f5ecd8;
  border-radius: 20px;
  padding: 5px 10px;
  border: 1px solid #d6bb70;
}

.total-pages {
  margin-left: 15px;
  color: #d6bb70;
  font-weight: bold;
  font-size: 14px;
}

.pagination-container ::v-deep .el-pagination {
  display: inline-flex;
  align-items: center;
  overflow-x: auto;
}

.pagination-container ::v-deep .btn-prev,
.pagination-container ::v-deep .btn-next {
  padding: 0 15px;
  border-radius: 15px;
  background: #f5ecd8;
  color: #d6bb70;
  border: none;
  margin: 0 5px;
  transition: all 0.3s;
}

.pagination-container ::v-deep .btn-prev:hover,
.pagination-container ::v-deep .btn-next:hover {
  background: #d6bb70;
  color: white;
}

.pagination-container ::v-deep .el-pager li {
  border-radius: 15px;
  background: #f5ecd8;
  margin: 0 5px;
  color: #d6bb70;
  transition: all 0.3s;
}

.pagination-container ::v-deep .el-pager li:hover {
  background: #e8d9b0;
  color: #b38b2d;
}

.pagination-container ::v-deep .el-pager li.active {
  background: #d6bb70;
  color: white;
  font-weight: bold;
}

/* 甜蜜风格警告弹窗 */
.sweet-warning-dialog :deep(.el-dialog) {
  border-radius: 20px;
  background: linear-gradient(135deg, #fefaf0, #f5ecd8);
  box-shadow: 0 10px 30px rgba(214, 187, 112, 0.3);
  overflow: hidden;
  border: 1px solid #d6bb70;
}

.sweet-warning-dialog :deep(.el-dialog__body) {
  padding: 30px 20px;
}

.warning-content {
  text-align: center;
}

.warning-icon {
  font-size: 40px;
  animation: float 3s ease-in-out infinite;
}

@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

.confirm-btn {
  background: #d6bb70;
  border: none;
  color: white;
  padding: 12px 30px;
  border-radius: 25px;
  font-weight: bold;
  transition: all 0.3s ease;
}

.confirm-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(214, 187, 112, 0.3);
  background: #c4a85f;
}

.confirm-btn:disabled {
  background: #f5ecd8;
  color: #d6bb70;
}
</style>