<!-- @/components/PetCakeForm.vue -->
<template>
  <el-form
    ref="formRef"
    :model="form"
    :rules="rules"
    label-width="100px"
    class="pet-cake-form"
  >
    <!-- 基础信息 -->
    <div class="form-section">
      <div class="section-title">宠物蛋糕信息</div>
      <el-form-item label="宠物蛋糕名称" prop="name" required>
        <el-input v-model="form.name" placeholder="例如：狗狗生日蛋糕" />
      </el-form-item>
      <el-form-item label="宠物蛋糕描述" prop="description" required>
        <el-input
          v-model="form.description"
          type="textarea"
          :rows="4"
          placeholder="请输入宠物蛋糕的详细描述，如适用宠物类型、口味、特点等（例如：低脂鸡肉配方，适合减肥期狗狗）"
        />
      </el-form-item>
    </div>

    <!-- 商品规格 -->
    <div class="form-section">
      <div class="section-title">商品规格</div>
      <div class="table-container">
        <el-table :data="form.specs" border style="width: 100%">
          <el-table-column label="规格描述" width="200">
            <template #default="{ row, $index }">
              <el-form-item :prop="`specs.${$index}.number`" :rules="requiredRule">
                <el-input v-model="row.number" placeholder="如：一个" />
              </el-form-item>
            </template>
          </el-table-column>
          <el-table-column label="价格(元)">
            <template #default="{ row, $index }">
              <el-form-item :prop="`specs.${$index}.price`" :rules="requiredRule">
                <el-input-number v-model="row.price" :min="0" :precision="2" style="width:100%" />
              </el-form-item>
            </template>
          </el-table-column>
          <el-table-column label="重量(g)">
            <template #default="{ row, $index }">
              <el-form-item :prop="`specs.${$index}.weight`" :rules="weightRule">
                <el-input-number v-model="row.weight" :min="100" :step="50" style="width:100%" />
              </el-form-item>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="100" align="center">
            <template #default="{ $index }">
              <el-button
                v-if="form.specs.length > 1"
                type="danger"
                size="small"
                icon="Delete"
                circle
                @click="removeSpec($index)"
              />
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>

    <!-- 蛋糕图片 -->
    <div class="form-section">
      <div class="section-title">宠物蛋糕图片</div>
      <!-- ✅ 修复：添加 prop="image" 和 label -->
      <el-form-item label="封面图片" prop="image">
        <el-upload
          action="/api/common/upload"
          :headers="uploadHeaders"
          :data="{ type: 'pet_cake' }"
          :on-success="handleUploadSuccess"
          :before-upload="beforeUpload"
          :show-file-list="false"
          accept="image/*"
        >
          <el-button type="primary">点击上传图片</el-button>
        </el-upload>
        <div v-if="form.image" class="image-preview">
          <img :src="form.image" alt="预览" style="height: 100px; margin-top: 10px" />
          <el-button size="small" type="danger" @click="form.image = null">删除</el-button>
        </div>
      </el-form-item>
    </div>

    <!-- 操作按钮 -->
    <el-form-item>
      <el-button type="primary" @click="submitForm">提交</el-button>
      <el-button @click="emit('cancel')">取消</el-button>
    </el-form-item>
  </el-form>
</template>

<script setup>
import { ref, watch, computed } from 'vue'
import { Delete } from '@element-plus/icons-vue'
import { useUserStore } from '@/store/user'
import { ElMessage } from 'element-plus'

const props = defineProps({
  isEdit: Boolean,
  initialData: Object
})
const emit = defineEmits(['submit', 'cancel'])

const userStore = useUserStore()
const formRef = ref(null)

// 表单数据
const form = ref({
  name: '',
  image: '',
  description: '',
  specs: [
    { number: '', price: 98, weight: 300 }
  ]
})

// ✅ 公共验证规则
const requiredRule = { required: true, message: '此项不能为空', trigger: 'blur' }
const weightRule = { required: true, message: '重量必须大于100g', trigger: 'blur' }

// ✅ 验证规则（关键）
const rules = {
  name: [
    { required: true, message: '请输入蛋糕名称', trigger: 'blur' },
    { min: 1, max: 50, message: '长度在 1 到 50 个字符', trigger: 'blur' }
  ],
  description: [
    { required: true, message: '请输入描述信息', trigger: 'blur' }
  ],
  image: [
    { required: true, message: '请上传封面图片', trigger: 'change' }
  ]
  // specs 规则已在表格内通过 requiredRule 单独控制，无需整体定义
}

// 上传配置
const uploadHeaders = computed(() => ({
  token: userStore.token
}))

const beforeUpload = (file) => {
  if (file.size / 1024 / 1024 > 2) {
    ElMessage.error('图片不能超过2MB')
    return false
  }
  return true
}

const handleUploadSuccess = (res) => {
  if (res.code === 200) {
    form.value.image = res.data
    ElMessage.success('上传成功')
  }
}

const removeSpec = (index) => {
  if (form.value.specs.length > 1) {
    form.value.specs.splice(index, 1)
  }
}

// ✅ 修复：正确使用 validate 回调
const submitForm = () => {
  formRef.value.validate((valid) => {
    if (valid) {
      emit('submit', { ...form.value })
    } else {
      ElMessage.error('请检查表单填写是否完整、正确！')
    }
  })
}

// 编辑模式初始化
watch(
  () => props.initialData,
  (val) => {
    if (val) {
      form.value = { ...val }
      // 确保 specs 存在
      if (!form.value.specs || form.value.specs.length === 0) {
        form.value.specs = [{ number: '', price: 98, weight: 300 }]
      }
    }
  },
  { immediate: true, deep: true }
)

// 暴露方法给父组件
const setFormData = (data) => {
  form.value = { ...data }
}
defineExpose({ setFormData })
</script>

<style scoped>
.section-title {
  font-weight: 600;
  margin-bottom: 15px;
  color: #8B6B3D;
  padding-bottom: 8px;
  border-bottom: 1px solid rgba(214, 187, 112, 0.3);
}
.pet-cake-form .form-section {
  background: #fefcf6;
  padding: 20px;
  border-radius: 8px;
  margin-bottom: 20px;
  border: 1px solid rgba(214, 187, 112, 0.3);
}
.table-container {
  width: 100%;
  overflow-x: auto;
}
.table-container :deep(.el-table) {
  min-width: 800px;
  width: 100% !important;
  border: 1px solid rgba(214, 187, 112, 0.3);
  border-radius: 6px;
}
.table-container :deep(.el-table th) {
  background: linear-gradient(to bottom, #f8f4e9, #f1ead5);
  color: #8B6B3D;
  font-weight: 600;
  border-bottom: 1px solid rgba(214, 187, 112, 0.3);
}
.table-container :deep(.el-table td) {
  white-space: nowrap;
  min-width: 120px;
  border-bottom: 1px solid rgba(214, 187, 112, 0.1);
}
.table-container :deep(.el-table__row:hover td) {
  background-color: #fefaf0;
}
.image-preview {
  margin-top: 10px;
}

/* 按钮样式优化 */
:deep(.el-button--primary) {
  background: linear-gradient(45deg, #d6bb70, #c4a85f);
  border: none;
  border-radius: 6px;
  transition: all 0.3s;
}
:deep(.el-button--primary:hover) {
  background: linear-gradient(45deg, #c4a85f, #b3984e);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(214, 187, 112, 0.4);
}

/* 表单元素样式优化 */
:deep(.el-input__wrapper) {
  border-radius: 6px;
  transition: all 0.3s;
}
:deep(.el-input__wrapper:hover) {
  border-color: #d6bb70;
  box-shadow: 0 0 0 1px rgba(214, 187, 112, 0.3);
}
:deep(.el-input__wrapper.is-focus) {
  border-color: #d6bb70;
  box-shadow: 0 0 0 1px rgba(214, 187, 112, 0.5);
}
:deep(.el-textarea__inner) {
  border-radius: 6px;
  transition: all 0.3s;
}
:deep(.el-textarea__inner:hover) {
  border-color: #d6bb70;
}
:deep(.el-textarea__inner:focus) {
  border-color: #d6bb70;
  box-shadow: 0 0 0 1px rgba(214, 187, 112, 0.3);
}

/* 取消按钮样式 */
:deep(.el-button:not(.el-button--primary)) {
  color: #8B6B3D;
  border: 1px solid rgba(214, 187, 112, 0.5);
  border-radius: 6px;
  transition: all 0.3s;
}
:deep(.el-button:not(.el-button--primary):hover) {
  background: rgba(214, 187, 112, 0.1);
  color: #745c2d;
  border-color: #d6bb70;
  transform: translateY(-2px);
}

/* 上传按钮特殊样式 */
:deep(.el-upload .el-button--primary) {
  background: linear-gradient(45deg, #d6bb70, #c4a85f);
  border: none;
}
:deep(.el-upload .el-button--primary:hover) {
  background: linear-gradient(45deg, #c4a85f, #b3984e);
}

/* 删除按钮样式 */
:deep(.el-button--danger) {
  background: #f56c6c;
  border: none;
}
:deep(.el-button--danger:hover) {
  background: #f78989;
  transform: scale(1.05);
}
</style>