import axios from 'axios'
import router from '@/router'
import { ElMessage } from 'element-plus'
import { useUserStore } from '@/store/user'

const request = axios.create({
    baseURL: 'http://localhost:8082',
    timeout: 30000,
    withCredentials: true // 设置跨域携带cookie
})

// 在入口文件（main.js）中添加
const ignoreError = window.onerror;
window.onerror = function(msg, source, lineno, colno, error) {
  if (msg.includes('ResizeObserver')) return true; // 忽略此错误
  return ignoreError?.apply(this, arguments);
};

// 请求拦截器
request.interceptors.request.use(config => {
      // 只在没有显式设置Content-Type时设置默认值
    if (!config.headers['Content-Type']) {
        config.headers['Content-Type'] = 'application/json;charset=utf-8'
    }
    const token = localStorage.getItem("token")||sessionStorage.getItem("token")
    console.log("token:",token)
    console.log("[Token 检查] sessionStorage:", sessionStorage.getItem("token"));
    console.log("[Token 检查] localStorage:", localStorage.getItem("token"));
    if (token) {
        config.headers['token'] = token // 设置请求头
        console.log('最终请求头配置:', JSON.parse(JSON.stringify(config.headers)))
        
    }
    return config
}, error => {
    return Promise.reject(error)
})

// 响应拦截器
request.interceptors.response.use(
    response => {
        let res = response.data
        // 如果是返回的文件
        if (response.config.responseType === 'blob') {
            return res
        }
        // 兼容服务端返回的字符串数据
        if (typeof res === 'string') {
            res = res ? JSON.parse(res) : res
        }
        
        
        return res
    },
    error => {
        const { response } = error
        const userStore = useUserStore()
        
        // 处理 Token 过期情况（401 未授权）
        if (response && response.status === 401) {
            // 1. 清除用户信息
            userStore.clearUser()
            
            // 2. 显示错误提示
            ElMessage.error('登录已过期，请重新登录')
            
            // 3. 跳转到登录页面
            router.push('/login')
            
            // 4. 阻止继续执行原请求的 catch 逻辑
            return Promise.reject(new Error('登录已过期'))
        }
        
        // 其他错误处理
        if (response && response.data && response.data.message) {
            ElMessage.error(response.data.message)
        } else {
            ElMessage.error(error.message || '请求失败')
        }
        
        return Promise.reject(error)
    }
)

export default request