<template>
  <div class="order-list-container">
    <div class="page-header">
      <h2>我的订单</h2>
      <p class="page-subtitle">查看和管理您的所有订单</p>
    </div>

    <!-- 筛选标签 -->
    <div class="filter-section">
      <el-radio-group v-model="statusFilter" @change="loadOrders">
        <el-radio-button :label="null">全部订单</el-radio-button>
        <el-radio-button :label="0">待支付</el-radio-button>
        <el-radio-button :label="1">已支付</el-radio-button>
        <el-radio-button :label="2">已取消</el-radio-button>
      </el-radio-group>
    </div>

    <!-- 订单列表 -->
    <div v-if="orders.length > 0" class="order-list">
      <el-card 
        shadow="hover" 
        v-for="order in orders" 
        :key="order.id" 
        class="order-card"
        :class="{'cancelled-order': order.payStatus === 2}"
      >
        <!-- 订单头部信息 -->
        <div class="order-header">
          <div class="order-info">
            <div class="order-no">
              <el-icon><Document /></el-icon>
              订单号：{{ order.orderNo }}
            </div>
            <el-tag class="status-tag" :type="statusTagType(order.payStatus)">
              {{ statusText(order.payStatus) }}
            </el-tag>
          </div>
          <div class="order-time">
            <el-icon><Clock /></el-icon>
            下单时间：{{ formatDate(order.orderTime) }}
          </div>
        </div>


        <!-- 新增：支付倒计时（仅待支付订单显示） -->
        <div v-if="order.payStatus === 0 && order.createTime" class="countdown-section">
          <div class="countdown-info">
            <el-icon><Clock /></el-icon>
            <span class="countdown-text">剩余支付时间：</span>
            <span class="countdown-timer" :class="getCountdownClass(order.remainingTime)">
              {{ formatCountdown(order.remainingTime) }}
            </span>
          </div>
          <div class="countdown-tip">超时未支付订单将自动取消</div>
        </div>

        <!-- 收货地址信息 -->
        <div class="address-section" v-if="order.address || order.consignee">
          <div class="address-header">
            <el-icon><Location /></el-icon>
            <span>收货地址</span>
          </div>
          <div class="address-content">
            <div class="address-line">
              <span class="address-label">收货人：</span>
              <span class="address-value">{{ order.consignee || '未填写' }}</span>
            </div>
            <div class="address-line">
              <span class="address-label">联系电话：</span>
              <span class="address-value">{{ order.phone || '未填写' }}</span>
            </div>
            <div class="address-line">
              <span class="address-label">详细地址：</span>
              <span class="address-value">{{ order.address || '未填写' }}</span>
            </div>
          </div>
        </div>

        <!-- 商品列表 -->
        <div class="products-section">
          <div class="section-title">商品信息</div>
          <div class="product-list">
            <div class="product-item" v-for="item in order.orderDetails" :key="item.id">
              <img :src="item.image" class="product-image" />
              <div class="product-info">
                <div class="product-name">{{ item.name }}</div>
                <div class="product-specs">
                  <span class="spec-item">规格：{{ item.number || '默认' }}</span>
                  <span class="spec-item">数量：×{{ item.num }}</span>
                  <span class="spec-item">单价：¥{{ item.amount.toFixed(2) }}</span>
                </div>
              </div>
            </div>
          </div>
        </div>

        <!-- 订单金额和备注 -->
        <div class="order-summary">
          <div class="order-remark" v-if="order.remark">
            <el-icon><ChatDotRound /></el-icon>
            订单备注：{{ order.remark }}
          </div>
          <div class="order-total">
            订单总额：<span class="total-amount">¥{{ order.totalAmount.toFixed(2) }}</span>
          </div>
        </div>

        <!-- 取消信息（仅当订单状态为"已取消"时显示） -->
        <div v-if="order.payStatus === 2" class="cancel-info">
          <div class="cancel-time">
            <el-icon><Clock /></el-icon>
            取消时间：{{ formatDate(order.cancelTime) }}
          </div>
          <div class="cancel-reason" v-if="order.cancelReason">
            <el-icon><Warning /></el-icon>
            取消原因：{{ order.cancelReason }}
          </div>
        </div>
        
        <!-- 操作按钮 -->
        <div class="order-actions">
          <el-button 
            class="action-btn detail-btn"
            @click="viewOrderDetail(order)"
          >
            <el-icon><View /></el-icon>
            订单详情
          </el-button>
          
          <!-- 待支付订单的操作按钮 -->
          <template v-if="order.payStatus === 0">
            <el-button
              class="action-btn cancel-btn"
              @click="cancelOrder(order)"
            >
              <el-icon><Close /></el-icon>
              取消订单
            </el-button>
            <el-button
              class="action-btn pay-btn"
              @click="payOrder(order)"
            >
              <el-icon><Money /></el-icon>
              立即支付
            </el-button>
          </template>
          
          <!-- 已支付订单的操作按钮 -->
          <template v-if="order.payStatus === 1">
            <el-button
              class="action-btn secondary"
              @click="contactService(order)"
            >
              <el-icon><Service /></el-icon>
              联系客服
            </el-button>
            <el-button
              class="action-btn secondary"
              @click="requestRefund(order)"
            >
              <el-icon><RefreshRight /></el-icon>
              申请退款
            </el-button>
          </template>
        </div>
      </el-card>
    </div>

    <!-- 空状态 -->
    <el-empty v-else description="暂无订单" :image-size="200">
      <el-button type="primary" @click="router.push('/home')"  class="shopping-btn">去首页逛逛</el-button>
    </el-empty>

    <!-- 查看详情弹窗 -->
    <el-dialog
      v-model="showDetailModal"
      :title="`订单详情 - ${selectedOrder?.orderNo || ''}`"
      width="700px"
      class="order-detail-dialog"
    >
      <div v-if="selectedOrder" class="order-detail">
        <div class="detail-section">
          <h3>订单信息</h3>
          <el-descriptions :column="2" border>
            <el-descriptions-item label="订单号">{{ selectedOrder.orderNo }}</el-descriptions-item>
            <el-descriptions-item label="下单时间">{{ formatDate(selectedOrder.orderTime) }}</el-descriptions-item>
            <el-descriptions-item label="订单状态">
              <el-tag :type="statusTagType(selectedOrder.payStatus)">{{ statusText(selectedOrder.payStatus) }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="支付方式">
              {{ selectedOrder.payMethod === 1 ? '微信支付' : '支付宝' }}
            </el-descriptions-item>
          </el-descriptions>
        </div>

        <div class="detail-section">
          <h3>收货信息</h3>
          <el-descriptions :column="1" border>
            <el-descriptions-item label="收货人">{{ selectedOrder.consignee || '未填写' }}</el-descriptions-item>
            <el-descriptions-item label="联系电话">{{ selectedOrder.phone || '未填写' }}</el-descriptions-item>
            <el-descriptions-item label="收货地址">{{ selectedOrder.address || '未填写' }}</el-descriptions-item>
          </el-descriptions>
        </div>

        <div class="detail-section">
          <h3>商品信息</h3>
          <el-table :data="selectedOrder.orderDetails" size="small" class="detail-table">
            <el-table-column prop="name" label="商品名称" width="200" />
            <el-table-column label="商品图片" width="80">
              <template #default="scope">
                <img :src="scope.row.image" class="table-product-image" />
              </template>
            </el-table-column>
            <el-table-column prop="number" label="规格" width="120" />
            <el-table-column prop="num" label="数量" width="80" />
            <el-table-column label="单价" width="100">
              <template #default="scope">
                ¥{{ scope.row.amount.toFixed(2) }}
              </template>
            </el-table-column>
            <el-table-column label="小计" width="100">
              <template #default="scope">
                ¥{{ (scope.row.amount * scope.row.num).toFixed(2) }}
              </template>
            </el-table-column>
          </el-table>
        </div>

        <div class="detail-total">
          <div class="total-line">
            <span class="total-label">订单总额：</span>
            <span class="total-value">¥{{ selectedOrder.totalAmount.toFixed(2) }}</span>
          </div>
        </div>

        <div class="detail-remark" v-if="selectedOrder.remark">
          <el-icon><ChatDotRound /></el-icon>
          订单备注：{{ selectedOrder.remark }}
        </div>
      </div>
    </el-dialog>

    <!-- 在模板的 order-list-container 内部添加 -->
    <el-dialog
      v-model="showCancelDialog"
      title="取消订单"
      width="500px"
    >
      <div class="cancel-dialog-content">
        <p class="cancel-tip">请选择取消原因：</p>
        
        <el-select
          v-model="cancelForm.cancelReason"
          placeholder="请选择取消原因"
          class="cancel-reason-select"
          size="large"
          @change="handleCancelReasonChange"
        >
          <el-option
            v-for="reason in cancelReasons"
            :key="reason.value"
            :label="reason.label"
            :value="reason.value"
          />
        </el-select>
        
        <el-input
          v-if="cancelForm.cancelReason === 'other'"
          v-model="cancelForm.otherReason"
          type="textarea"
          placeholder="请输入其他原因"
          :rows="3"
          maxlength="200"
          show-word-limit
          style="margin-top: 16px;"
        />
      </div>
      
      <template #footer>
        <div class="dialog-footer">
          <el-button @click="showCancelDialog = false" :disabled="canceling">
            再想想
          </el-button>
          <el-button 
            type="primary" 
            @click="confirmCancelOrder" 
            :loading="canceling"
            :disabled="!cancelForm.cancelReason || (cancelForm.cancelReason === 'other' && !cancelForm.otherReason)"
          >
            确认取消
          </el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, onMounted ,onUnmounted,reactive } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { 
  Document, 
  Clock, 
  Location, 
  ChatDotRound, 
  Warning, 
  View, 
  Close, 
  Money,
  Service,
  RefreshRight
} from '@element-plus/icons-vue'
import { getOrderList, cancelOrder as apiCancelOrder, payOrder as apiPayOrder} from '@/api/order'
import { useRouter } from 'vue-router'

const router = useRouter()



// 弹窗相关
const showDetailModal = ref(false)
const selectedOrder = ref(null)


// 筛选条件
const statusFilter = ref(null)

// 订单列表
const orders = ref([])

// 定时器
let countdownTimer = null

// 加载订单列表
const loadOrders = async () => {
  const params = {}
  
  if (statusFilter.value === 0 || statusFilter.value === 1 || statusFilter.value === 2) {
    params.payStatus = statusFilter.value
  }

  try {
    const res = await getOrderList(params)
    orders.value = res.data || []
    
    // 使用 map 创建新数组并重新赋值
    orders.value = orders.value.map(order => {
      if (order.payStatus === 0 && order.createTime) {
        const remainingTime = calculateRemainingTime(order.createTime)
        return {
          ...order,
          remainingTime,
          needRefresh: remainingTime <= 0
        }
      }
      return order
    })
    
    // 启动定时器
    startCountdownTimer()
  } catch (error) {
    ElMessage.error('加载订单失败')
    console.error('请求失败:', error)
  }
}

// 计算订单剩余支付时间（30分钟）
const calculateRemainingTime = (createTime) => {
  if (!createTime) return 0

  let createDate
 // 如果是数组格式 [年, 月, 日, 时, 分, 秒]
  if (Array.isArray(createTime) && createTime.length >= 6) {
    // 注意：月份从 0 开始，所以需要减 1
    createDate = new Date(
      createTime[0], // 年
      createTime[1] - 1, // 月 (0-11)
      createTime[2], // 日
      createTime[3], // 时
      createTime[4], // 分
      createTime[5]  // 秒
    )
  } else {
    // 其他格式尝试直接解析
    createDate = new Date(createTime)
  }
  const now = new Date()
  const timeDiff = now - createDate  // 毫秒
  
  // 30分钟 = 30 * 60 * 1000 = 1800000 毫秒
  const totalTime = 30 * 60 * 1000
  const remainingTime = Math.max(0, totalTime - timeDiff)
  
  return Math.floor(remainingTime / 1000)  // 转换为秒
}

// 更新单个订单的剩余时间
const updateOrderRemainingTime = (order) => {
  if (order.payStatus !== 0) return
  
  const remainingSeconds = calculateRemainingTime(order.createTime)
  order.remainingTime = remainingSeconds
  
  // 如果时间到了，标记为需要刷新
  if (remainingSeconds <= 0) {
    order.needRefresh = true
  }
}

// 格式化倒计时显示
const formatCountdown = (seconds) => {
  if (seconds <= 0) return '00:00:00'
  
  const hours = Math.floor(seconds / 3600)
  const minutes = Math.floor((seconds % 3600) / 60)
  const secs = seconds % 60
  
  return `${hours.toString().padStart(2, '0')}:${minutes.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`
}

// 根据剩余时间获取样式类名
const getCountdownClass = (seconds) => {
  if (seconds <= 300) { // 5分钟
    return 'countdown-urgent'
  } else if (seconds <= 600) { // 10分钟
    return 'countdown-warning'
  }
  return 'countdown-normal'
}

// 启动倒计时定时器
// 启动倒计时定时器
const startCountdownTimer = () => {
  if (countdownTimer) {
    clearInterval(countdownTimer)
  }
  
  countdownTimer = setInterval(() => {
    let needRefresh = false
    
    orders.value.forEach(order => {
      if (order.payStatus === 0 && order.remainingTime !== undefined) {
        // 直接减少剩余时间，而不是重新计算
        order.remainingTime = Math.max(0, order.remainingTime - 1)
        
        // 如果时间到0且需要刷新
        if (order.remainingTime <= 0 && !order.needRefresh) {
          order.needRefresh = true
          needRefresh = true
        }
      }
    })
    
    // 如果有订单超时，刷新列表
    if (needRefresh) {
      loadOrders()
    }
  }, 1000)
}

// 停止定时器
const stopCountdownTimer = () => {
  if (countdownTimer) {
    clearInterval(countdownTimer)
    countdownTimer = null
  }
}

// 查看详情
const viewOrderDetail = (order) => {
  selectedOrder.value = order
  showDetailModal.value = true
}

// 添加响应式数据
const showCancelDialog = ref(false)
const canceling = ref(false)
const selectedOrderForCancel = ref(null)
const cancelForm = reactive({
  cancelReason: '',
  otherReason: ''
})

const cancelReasons = [
  { value: 'change_mind', label: '不想买了' },
  { value: 'wrong_info', label: '信息填写错误' },
  { value: 'found_cheaper', label: '发现更便宜的商品' },
  { value: 'out_of_stock', label: '商品缺货' },
  { value: 'delivery_issue', label: '配送时间不合适' },
  { value: 'payment_issue', label: '支付遇到问题' },
  { value: 'other', label: '其他原因' }
]

// 修改取消订单方法
const cancelOrder = (order) => {
  selectedOrderForCancel.value = order
  // 重置表单
  cancelForm.cancelReason = ''
  cancelForm.otherReason = ''
  showCancelDialog.value = true
}

// 处理取消原因变化
const handleCancelReasonChange = (value) => {
  if (value !== 'other') {
    cancelForm.otherReason = ''
  }
}

// 确认取消订单
const confirmCancelOrder = async () => {
  if (!selectedOrderForCancel.value) return
  
  if (!cancelForm.cancelReason) {
    ElMessage.error('请选择取消原因')
    return
  }

  if (cancelForm.cancelReason === 'other' && !cancelForm.otherReason.trim()) {
    ElMessage.error('请输入其他原因')
    return
  }

  canceling.value = true
  try {
    const cancelData = {
      id: selectedOrderForCancel.value.id,
      cancelReason: cancelForm.cancelReason === 'other' ? 
                   cancelForm.otherReason.trim() : 
                   cancelReasons.find(r => r.value === cancelForm.cancelReason)?.label
    }

    await apiCancelOrder(cancelData)
    ElMessage.success('订单已取消')
    showCancelDialog.value = false
    loadOrders()
  } catch (error) {
    ElMessage.error('取消失败')
  } finally {
    canceling.value = false
  }
}

// 去支付
const payOrder = (order) => {
  

   

    router.push({
      path:"/simplePay",
      query:{
        id: order.id,
        amount:order.totalAmount,
        orderNo: order.orderNo

      }
    })

    
    loadOrders()
  }


// 联系客服
const contactService = (order) => {
  ElMessage.info(`请联系客服处理订单 ${order.orderNo}`)
}

// 申请退款
const requestRefund = (order) => {
  ElMessageBox.confirm(`确定要为订单 ${order.orderNo} 申请退款吗？`, '申请退款', {
    type: 'warning',
    confirmButtonText: '申请退款',
    cancelButtonText: '取消'
  }).then(() => {
    ElMessage.success('退款申请已提交，客服将在24小时内处理')
  }).catch(() => {
    ElMessage.info('取消申请退款')
  })
}

// 订单状态文本
const statusText = (status) => {
  switch (status) {
    case 0: return '待支付'
    case 1: return '已支付'
    case 2: return '已取消'
    default: return '未知'
  }
}

// Tag 类型
const statusTagType = (status) => {
  switch (status) {
    case 0: return 'warning'
    case 1: return 'success'
    case 2: return 'info'
    default: return ''
  }
}

const formatDate = (input) => {
  let date

  if (!input) return ''

  if (Array.isArray(input)) {
    const [year, month, day, hour = 0, minute = 0, second = 0] = input
    date = new Date(year, month - 1, day, hour, minute, second)
  } else {
    date = new Date(input)
  }

  if (isNaN(date.getTime())) return '无效日期'

  const pad = (n) => n.toString().padStart(2, '0')
  return `${date.getFullYear()}-${pad(date.getMonth() + 1)}-${pad(date.getDate())} ${pad(date.getHours())}:${pad(date.getMinutes())}:${pad(date.getSeconds())}`
}

onMounted(() => {
  loadOrders()
})

onUnmounted(() => {
  stopCountdownTimer()
})
</script>

<style scoped>

/* 倒计时区域样式 */
.countdown-section {
  margin: 0 0 20px 0;
  padding: 12px 16px;
  background: rgba(255, 243, 224, 0.6);
  border-radius: 8px;
  border: 1px solid #ffe7ba;
}

.countdown-info {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 6px;
}

.countdown-text {
  color: #e6a23c;
  font-size: 14px;
  font-weight: 500;
}

.countdown-timer {
  font-family: 'Courier New', monospace;
  font-weight: bold;
  font-size: 16px;
}

.countdown-normal {
  color: #67c23a;
}

.countdown-warning {
  color: #e6a23c;
}

.countdown-urgent {
  color: #f56c6c;
  animation: blink 1s infinite;
}

@keyframes blink {
  0%, 50% { opacity: 1; }
  51%, 100% { opacity: 0.5; }
}

.countdown-tip {
  color: #8a7c5a;
  font-size: 12px;
  margin: 0;
}
.order-list-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
  background: linear-gradient(135deg, #fdf6e3 0%, #faf4eb 100%);
  min-height: 100vh;
}

.page-header {
  text-align: center;
  margin-bottom: 30px;
}

.page-header h2 {
  margin: 0;
  color: #b38b2d;
  font-size: 28px;
  font-weight: 600;
  background: linear-gradient(135deg, #d6bb70, #b38b2d);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.page-subtitle {
  margin: 8px 0 0;
  color: #8a7c5a;
  font-size: 14px;
}

.filter-section {
  margin-bottom: 25px;
  text-align: center;
}

:deep(.el-radio-group) {
  background: rgba(255, 253, 247, 0.8);
  padding: 8px;
  border-radius: 12px;
  border: 1px solid #e8dfc8;
}

:deep(.el-radio-button) {
  margin: 0 4px;
}

:deep(.el-radio-button__inner) {
  border-radius: 8px;
  border: 1px solid #e8dfc8;
  background: white;
  color: #8a7c5a;
  transition: all 0.3s ease;
}

:deep(.el-radio-button__inner:hover) {
  color: #d6bb70;
  border-color: #d6bb70;
}

:deep(.el-radio-button__original-radio:checked + .el-radio-button__inner) {
  background: linear-gradient(135deg, #d6bb70, #c4a85f);
  border-color: #d6bb70;
  color: white;
  box-shadow: 0 2px 8px rgba(214, 187, 112, 0.3);
}

.order-list {
  margin-top: 20px;
}

.order-card {
  margin-bottom: 20px;
  border-radius: 16px;
  border: 1px solid #e8dfc8;
  background: rgba(255, 253, 247, 0.9);
  backdrop-filter: blur(5px);
  transition: all 0.3s ease;
}

.order-card:hover {
  box-shadow: 0 6px 20px rgba(214, 187, 112, 0.15);
  transform: translateY(-2px);
}

.cancelled-order {
  opacity: 0.8;
  background: linear-gradient(135deg, #f8f8f8 0%, #f0f0f0 100%);
}

.order-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  border-bottom: 1px solid #f0e9d7;
  background: linear-gradient(135deg, #f9f3e5 0%, #f5ecd8 100%);
  margin: -20px -20px 20px -20px;
  border-radius: 16px 16px 0 0;
}

.order-info {
  display: flex;
  align-items: center;
