<template>
  <div class="nav-container">
    <div class="nav-content">
      <!-- 网站Logo -->
      <div class="logo">
      <div>
          <img src="@/assets/images/logo.png" alt="金苹果烘培">
          <span class="brand-text">金苹果烘培</span>
      </div>

      </div>

      <!-- 主导航菜单 -->
      <div class="main-menu">
        <el-menu
          mode="horizontal"
          :default-active="activeIndex"
          router
          @select="handleSelect"
          class="sweet-menu"
        >
          <el-menu-item index="/home">首页</el-menu-item>
          <el-menu-item index="/cakes">蛋糕甄选</el-menu-item>
          <el-menu-item index="/desserts">甜品系列</el-menu-item>
          <el-menu-item index="/pet">宠物蛋糕</el-menu-item>
          <el-menu-item index="/cooperation">合作专区</el-menu-item>
          <el-menu-item index="/us">关于我们</el-menu-item>
        </el-menu>
      </div>

      <!-- 右侧功能图标 -->
      <div class="action-icons">
        <div class="icon-item" @click="showSearch = true">
          <el-icon class="sweet-icon"><Search /></el-icon>
          <span>搜索</span>
        </div>
        <div class="icon-item" @click="goToCart">
          <el-badge :value="cartCount" :max="99" :hidden="cartCount === 0" class="sweet-badge">
            <el-icon class="sweet-icon"><ShoppingCart /></el-icon>
          </el-badge>
          <span>购物车</span>
        </div>
        <el-dropdown trigger="click" @command="handleCommand" class="sweet-dropdown">
          <div class="icon-item">
            <el-icon class="sweet-icon"><User /></el-icon>
            <span>我的</span>
          </div>
          <template #dropdown>
            <el-dropdown-menu class="sweet-dropdown-menu">
              <el-dropdown-item command="person" class="sweet-dropdown-item">
                <el-icon><UserFilled /></el-icon> 个人中心
              </el-dropdown-item>
              <el-dropdown-item command="orders" class="sweet-dropdown-item">
                <el-icon><Tickets /></el-icon> 我的订单
              </el-dropdown-item>
              <el-dropdown-item divided command="logout" class="sweet-dropdown-item">
                <el-icon><SwitchButton /></el-icon> 退出登录
              </el-dropdown-item>
            </el-dropdown-menu>
          </template>
        </el-dropdown>
      </div>
    </div>

    <!-- 搜索弹窗 -->
    <el-dialog 
      v-model="showSearch" 
      title="搜索甜蜜美味" 
      width="30%" 
      center
      class="sweet-dialog"
    >
      <el-input 
        v-model="searchQuery" 
        placeholder="请输入您想寻找的蛋糕或甜品..." 
        clearable
        @keyup.enter="handleSearch"
        class="sweet-search-input"
      >
        <template #append>
          <el-button :icon="Search" @click="handleSearch" class="sweet-search-btn" />
        </template>
      </el-input>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref } from 'vue'
import { useRouter } from 'vue-router'
import { useUserStore } from '@/store/user'
import { 
  Search, 
  ShoppingCart, 
  User,
  UserFilled,
  Tickets,
  SwitchButton
} from '@element-plus/icons-vue'
import { ElMessage } from 'element-plus'
import { logout } from '@/api/auth'

const router = useRouter()
const activeIndex = ref('/')
const cartCount = ref(0)
const showSearch = ref(false)
const searchQuery = ref('')
const userStore = useUserStore()

const handleSelect = (index) => {
  activeIndex.value = index
}

const goToCart = () => {
  router.push('/cart')
}

const handleSearch = () => {
  if (searchQuery.value.trim()) {
    router.push(`/search?q=${encodeURIComponent(searchQuery.value)}`)
    showSearch.value = false
    searchQuery.value = ''
  }
}

const handleCommand = async (command) => {
  switch (command) {
    case 'person':
      router.push('/person')
      break
    case 'orders':
      router.push('/order')
      break
    case 'logout':
      try {
        await logout()
        userStore.clearUser()
        ElMessage.success('退出成功')
        router.push('/login')
      } catch (error) {
        ElMessage.error('退出失败')
      }
      break
  }
}
</script>

