.logo {
  margin-right: 50px;
  display: flex;
  align-items: center;
}
.logo a {
  display: flex;
  align-items: center;
  text-decoration: none;
}
.logo img {
  height: 38px;
  transition: all 0.3s;
}
.brand-text {
  font-size: 20px;
  font-weight: bold;
  color: #d6bb70;
  margin-left: 12px;
  letter-spacing: 1px;
  text-shadow: 0 2px 4px rgba(214, 187, 112, 0.2);
  transition: all 0.3s;
}
.logo:hover img {
  transform: scale(1.05);
  opacity: 0.9;
}
.logo:hover .brand-text {
  color: #b69250;
  text-shadow: 0 2px 8px rgba(182, 146, 80, 0.3);
}

/* 主菜单样式 */
.main-menu {
  flex: 1;
}

/* 菜单项样式 - 适配浅金色背景 */
:deep(.sweet-menu) {
  border-bottom: none;
  background: transparent;
}
:deep(.sweet-menu.el-menu--horizontal) {
  display: flex;
  justify-content: center;
}
:deep(.sweet-menu .el-menu-item) {
  font-size: 16px;
  font-weight: 500;
  color: #8B6B3D;
  margin: 0 12px;
  padding: 0 12px;
  height: 72px;
  line-height: 72px;
  transition: all 0.3s;
  border-bottom: 3px solid transparent;
}
:deep(.sweet-menu .el-menu-item:hover) {
  color: #d6bb70;
  background: transparent !important;
  border-bottom-color: rgba(214, 187, 112, 0.5);
}
:deep(.sweet-menu .el-menu-item.is-active) {
  color: #d6bb70;
  border-bottom-color: #d6bb70;
  background: transparent !important;
}

/* 主导航菜单样式 - 加强金色主题 */
.main-menu {
  flex: 1;
}

/* 完全覆盖 Element Plus 默认蓝色主题 */
:deep(.sweet-menu) {
  border-bottom: none;
  background: transparent;
}

:deep(.sweet-menu.el-menu--horizontal) {
  display: flex;
  justify-content: center;
}

:deep(.sweet-menu .el-menu-item) {
  font-size: 16px;
  font-weight: 500;
  color: #8B6B3D;
  margin: 0 12px;
  padding: 0 12px;
  height: 72px;
  line-height: 72px;
  transition: all 0.3s;
  border-bottom: 3px solid transparent;
}

:deep(.sweet-menu .el-menu-item:hover) {
  color: #d6bb70;
  background: transparent !important;
  border-bottom-color: rgba(214, 187, 112, 0.5);
}

/* 关键：强制覆盖激活状态为金色 */
