:deep(.sweet-menu .el-menu-item.router-link-active),
:deep(.sweet-menu .el-menu-item.is-active:hover),
:deep(.sweet-menu .el-menu-item.router-link-active:hover) {
  color: #d6bb70 !important;
  border-bottom-color: #d6bb70 !important;
  background: transparent !important;
}

/* 右侧图标区域 - 适配浅金色背景 */
.action-icons {
  display: flex;
