
import request from '@/utils/request'

// 添加到购物车
export function addToCart(data){
    return request({
        url:'/shoppingCart/add',
        method: 'put',
        data: data
    })
}
  


// 获取购物车列表
export function getCartItems() {
  return request({
    url: '/shoppingCart/list',
    method: 'get'
  })
}

// 更新数量
export function updateCart (data){
  return request({
    url: '/shoppingCart/update',
    method: 'post',
    data: data
  })
}

// 删除某一项
export function removeCart(id) {
  return request({
    url: `/shoppingCart/remove/${id}`,
    method: 'delete',
    
  })
  
}

//批量删除
export function batchRemoveCart(ids){
  return request({
    url: '/shoppingCart/batchRemove',
    method: 'delete',
    data: ids
  })
}

//清空购物车
export function clearCart(){
  return request({
    url: '/shoppingCart/clearCart',
    method: 'delete'
  })
}