<template>
  <div class="checkout-container">
    <h2>确认订单</h2>

    <!-- 商品列表 -->
    <el-card shadow="never" style="margin-bottom: 20px;" class="checkout-card">
      <h3>商品信息</h3>
      <div v-for="item in items" :key="item.id" class="product-item">
        <img :src="item.image" class="product-image" />
        <span class="product-name">{{ item.name }}</span>
        <span class="product-number">({{ item.number }})</span>
        <span class="product-quantity">×{{ item.num }}</span>
        <span class="product-price">¥{{ (item.amount * item.num).toFixed(2) }}</span>
      </div>
    </el-card>

    <!-- 收货地址 -->
    <el-card shadow="never" style="margin-bottom: 20px;" class="checkout-card">
      <h3>收货地址</h3>
      <el-radio-group v-model="selectedAddressId">
        <div v-for="address in addresses" :key="address.id" class="address-item">
          <el-radio :label="address.id" class="address-radio">
            {{ address.consignee }} {{ address.sex }} {{ address.phone }} | 
            {{ address.provinceName }} {{ address.cityName }} {{ address.districtName }} {{ address.detail }}
            <el-link @click.stop="editAddress(address)" class="edit-link">编辑</el-link>
          </el-radio>
        </div>

        <div class="add-address-btn">
          <el-button @click="addNewAddress" class="add-btn">
            + 添加新地址
          </el-button>
        </div>
      </el-radio-group>

      <!-- 地址弹窗 -->
      <el-dialog
        v-model="showAddressForm"
        :title="editingAddress ? '编辑收货地址' : '添加新地址'"
        width="600px"
        @close="onDialogClose"
        class="address-dialog"
      >
        <AddressForm
          ref="addressFormRef"
          :address="editingAddress"
          size="small"
          @submit="handleAddressSubmit"
          @cancel="handleCancel"
        />
        <template #footer>
          <el-button @click="handleCancel" class="cancel-btn">取消</el-button>
          <el-button type="primary" @click="addressFormRef.submit()" class="confirm-btn">
            {{ editingAddress ? '更新' : '保存' }}
          </el-button>
        </template>
      </el-dialog>
    </el-card>

    

    <!-- 订单备注 -->
    <el-card shadow="never" style="margin-bottom: 20px;" class="checkout-card">
      <h3>订单备注</h3>
      <el-input
        v-model="remark"
        type="textarea"
        :rows="3"
        placeholder="选填：请填写对订单的特殊要求，如配送时间、口味偏好等"
        maxlength="200"
        show-word-limit
        class="remark-input"
      />
    </el-card>

    <!-- 总价 -->
    <div class="total-price-section">
      总计：<span class="total-price">¥{{ totalPrice }}</span>
    </div>

    <!-- 提交按钮 -->
    <div class="action-buttons">
      <el-button @click="$router.back()" class="back-btn">返回购物车</el-button>
      <el-button @click="handleCreateOrder" :loading="loading" class="submit-btn">
        提交订单
      </el-button>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { ElMessage } from 'element-plus'
import { createOrder } from '@/api/order'
import { addAddress, getAddressList, updateAddress } from '@/api/address'
import AddressForm from '@/components/AddressForm.vue'

const route = useRoute()
const router = useRouter()

// 商品数据
const itemsStr = route.query.items
const items = ref([])
if (itemsStr) {
  try {
    items.value = JSON.parse(decodeURIComponent(itemsStr))
    console.log("商品数据:",items.value)
  } catch (error) {
    console.error('解析商品数据失败', error)
  }
}

// 计算总价
const totalPrice = computed(() => {
  return items.value
    .reduce((sum, item) => sum + item.amount * item.num, 0)
    .toFixed(2)
})

// 地址相关
const addresses = ref([])
const selectedAddressId = ref(null)

const showAddressForm = ref(false)
const editingAddress = ref(null)
const addressFormRef = ref()

// 订单备注
const remark = ref('')

const editAddress = (address) => {
  editingAddress.value = address
  showAddressForm.value = true
}

const addNewAddress = () => {
  editingAddress.value = null
  showAddressForm.value = true
}

const onDialogClose = () => {
  if (addressFormRef.value) {
    addressFormRef.value.resetForm()
  }
  editingAddress.value = null
}

const handleCancel = () => {
  showAddressForm.value = false
  editingAddress.value = null
  if (addressFormRef.value) {
    addressFormRef.value.resetForm()
  }
}

const handleAddressSubmit = async (data) => {
  if (editingAddress.value) {
    try {
      await updateAddress(data)
      const index = addresses.value.findIndex(a => a.id === data.id)
      if (index !== -1) addresses.value.splice(index, 1, data)
      ElMessage.success('地址更新成功')
    } catch (error) {
      ElMessage.error("地址更新失败")
    }
  } else {
    try {
      const res = await addAddress(data)
      addresses.value.push(res.data)
      if (data.isDefault) selectedAddressId.value = res.data.id
      ElMessage.success('地址添加成功')
    } catch (error) {
      ElMessage.error("地址添加失败")
    }
  }
  if (data.isDefault) selectedAddressId.value = data.id
  showAddressForm.value = false
  editingAddress.value = null
}

const loadAddresses = async () => {
  try {
    const res = await getAddressList()
    addresses.value = res.data

    const defaultAddress = addresses.value.find(addr => addr.isDefault === 1 || addr.isDefault === true)
    if (defaultAddress) {
      selectedAddressId.value = defaultAddress.id
    } else if (addresses.value.length > 0) {
      selectedAddressId.value = addresses.value[0].id
    }
  } catch (err) {
    ElMessage.error('加载地址失败')
  }
}

// 支付方式：1=微信，2=支付宝
// const selectedPayment = ref(1) // 默认选中微信支付
// const paymentMethod = ref(1)   // 实际提交的值

// 点击切换支付方式
// const selectPayment = (type) => {
//   selectedPayment.value = type
//   paymentMethod.value = type
//   console.log(`用户选择了支付方式: ${type === 1 ? '微信支付' : '支付宝'}`)
// }

// 提交订单
const loading = ref(false)
const handleCreateOrder = async () => {
  const selectedAddress = addresses.value.find(addr => addr.id === selectedAddressId.value)
  if (!selectedAddress) {
    ElMessage.warning('地址信息异常')
    loading.value = false
    return
  }
  if (!selectedAddressId.value) {
    ElMessage.warning('请选择收货地址')
    return
  }

  loading.value = true
  try {
    const orderData = {
      phone: selectedAddress.phone,
      consignee: selectedAddress.consignee,
      address: selectedAddress.provinceName+selectedAddress.cityName+selectedAddress.districtName+selectedAddress.detail,
      addressId: selectedAddressId.value,
      totalAmount: parseFloat(totalPrice.value),
      // payMethod: paymentMethod.value,
      remark: remark.value,
      orderDetails: items.value.map(item => ({
        name : item.name,
        productId: item.productId,
        image: item.image,
        num: item.num,
        amount: item.amount,
        number: item.number
      })
    )
    }

    console.log("订单信息：",orderData)
    const res = await createOrder(orderData)
    ElMessage.success('订单创建成功')
    router.push({
      path:'/pay',
      query:{
        orderId: res.data.id,           // 订单ID
        orderNo: res.data.orderNo,      // 订单号
        amount: res.data.totalAmount,   // 订单金额
      }
    })

  } catch (error) {
    ElMessage.error('订单创建失败，请重试')
  } finally {
    loading.value = false
  }
}

onMounted(() => {
  loadAddresses()
})
</script>

<style scoped>
.checkout-container {
  max-width: 1000px;
  margin: 40px auto;
  padding: 20px;
  background-color: #fefaf0;
}

h2 {
  color: #d6bb70;
  text-align: center;
  margin-bottom: 30px;
  font-weight: 600;
}

h3 {
  color: #d6bb70;
  margin-bottom: 16px;
  font-weight: 500;
  border-left: 4px solid #d6bb70;
  padding-left: 12px;
}

/* 卡片样式 */
.checkout-card {
  background-color: #fffdf7;
  border: 1px solid #f5ecd8;
  border-radius: 12px;
}

/* 商品项样式 */
.product-item {
  display: flex;
  align-items: center;
  margin: 16px 0;
  padding: 12px;
  background-color: #fefaf0;
  border-radius: 8px;
  border: 1px solid #f5ecd8;
}

.product-image {
  width: 60px;
  height: 60px;
  border-radius: 4px;
  margin-right: 12px;
  border: 1px solid #f5ecd8;
}

.product-name {
  font-weight: 500;
  color: #333;
  margin-right: 8px;
}

.product-number {
  color: #999;
  font-size: 13px;
  margin-right: 8px;
}

.product-quantity {
  color: #d6bb70;
  font-weight: 500;
  margin-right: auto;
}

.product-price {
  color: #d6bb70;
  font-weight: bold;
  font-size: 16px;
}

/* 地址项样式 */
.address-item {
  margin: 12px 0;
  padding: 16px;
  background-color: #fefaf0;
  border-radius: 8px;
  border: 1px solid #f5ecd8;
}

.address-radio {
  width: 100%;
}

:deep(.address-radio .el-radio__label) {
  color: #666;
}

:deep(.address-radio .el-radio__input.is-checked .el-radio__inner) {
  background-color: #d6bb70;
  border-color: #d6bb70;
}

.edit-link {
  margin-left: 12px;
  color: #d6bb70;
}

.edit-link:hover {
  color: #c4a85f;
}

.add-address-btn {
  margin: 16px 0;
}

.add-btn {
  background-color: #f5ecd8;
  color: #d6bb70;
  border: 1px solid #d6bb70;
  border-radius: 8px;
  transition: all 0.3s;
}

.add-btn:hover {
  background-color: #d6bb70;
  color: white;
}

/* 支付选项样式 */
.payment-option {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 16px;
  border: 1px solid #f5ecd8;
  border-radius: 8px;
  cursor: pointer;
  margin: 12px 0;
  transition: all 0.3s;
  background-color: #fefaf0;
}

.payment-option:hover {
  background-color: #f5ecd8;
  border-color: #d6bb70;
}

.payment-option.active {
  border-color: #d6bb70;
  background-color: #f5ecd8;
  box-shadow: 0 2px 8px rgba(214, 187, 112, 0.2);
}

.payment-option img {
  width: 40px;
  height: 40px;
}

.payment-option span {
  font-size: 16px;
  color: #333;
  font-weight: 500;
}

/* 备注输入框 */
.remark-input :deep(.el-textarea__inner) {
  background-color: #fefaf0;
  border: 1px solid #f5ecd8;
  border-radius: 8px;
}

.remark-input :deep(.el-textarea__inner:focus) {
  border-color: #d6bb70;
}

/* 总价区域 */
.total-price-section {
  text-align: right;
  font-size: 18px;
  margin-bottom: 20px;
  padding: 20px;
  background-color: #fffdf7;
  border-radius: 8px;
  border: 1px solid #f5ecd8;
}

.total-price {
  color: #d6bb70;
  font-weight: bold;
  font-size: 24px;
}

/* 操作按钮 */
.action-buttons {
  text-align: right;
  padding-top: 20px;
  border-top: 1px solid #f5ecd8;
}

.back-btn {
  background-color: #f5ecd8;
  color: #d6bb70;
  border: 1px solid #d6bb70;
  border-radius: 8px;
  padding: 12px 24px;
  transition: all 0.3s;
}

.back-btn:hover {
  background-color: #e8d9b0;
  border-color: #c4a85f;
}

.submit-btn {
  background-color: #d6bb70;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 12px 24px;
  font-weight: 500;
  transition: all 0.3s;
}

.submit-btn:hover {
  background-color: #c4a85f;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(214, 187, 112, 0.3);
}

.submit-btn:active {
  transform: translateY(0);
}

/* 弹窗样式 */
:deep(.address-dialog .el-dialog) {
  border-radius: 12px;
  background-color: #fffdf7;
  border: 1px solid #f5ecd8;
}

:deep(.address-dialog .el-dialog__header) {
  background-color: #fefaf0;
  border-bottom: 1px solid #f5ecd8;
  border-radius: 12px 12px 0 0;
}

:deep(.address-dialog .el-dialog__title) {
  color: #d6bb70;
  font-weight: 600;
}

.cancel-btn {
  background-color: #f5ecd8;
  color: #d6bb70;
  border: 1px solid #d6bb70;
  border-radius: 8px;
}

.cancel-btn:hover {
  background-color: #e8d9b0;
  border-color: #c4a85f;
}

.confirm-btn {
  background-color: #d6bb70;
  border: none;
  border-radius: 8px;
  color: white;
}

.confirm-btn:hover {
  background-color: #c4a85f;
}

/* 响应式优化 */
@media (max-width: 768px) {
  .checkout-container {
    margin: 20px auto;
    padding: 10px;
  }

  .product-item {
    flex-wrap: wrap;
    gap: 8px;
  }

  .product-price {
    margin-left: auto;
  }

  .action-buttons {
    text-align: center;
  }

  .back-btn, .submit-btn {
    width: 100%;
    margin-bottom: 8px;
  }
}
</style>